<template>
  <div class="use_add_manage">
      <van-radio-group
      
       v-model="radio">
      <div class="addr"
      v-for="(v,i) in addressData"
      :key="i"
      >
          <div class="top">
              <div class="name">收货人：{{v.real_name}}</div>
              <div class="address">收货地址：{{v.province+v.city+v.district+v.detail}}</div>
          </div>
          <div class="bottom">
              <div class="left">
                   <van-radio
                   @click="select_change(v.id)"
                    :name="v.id"  checked-color="#ee0a24" > 设为默认</van-radio>
              </div>
              <div class="right">
                  <div @click="edit_addr(v.id)" class="edit fa fa-edit"> 编辑</div>
                  <div
                  @click="del_addr(v.id)"
                   class="trash fa fa-trash-o "> 删除</div>
              </div>
          </div>
      </div>
      
      </van-radio-group>
      <div class="add">
          <div class="fa fa-map-marker" @click="onAdd"> 添加新地址</div>
      </div>
  </div>
</template>


<script>
export default {
  data() {
    return {
      addressData: [],
      radio:'1'
    };
  },
  computed: {
  },
  created() {
    this.mix_set_data("/address/list?page=1&limit=20", "addressData");
  },
  methods: {
    onAdd() {
      this.$router.push("/user/add_address/ ");
    },
    select_change(name){
       this.axios.post("/address/default/set",{
           id:name
       }).then(data => {
        //    if(data.data.status)
       if(data.data.status == 200){
           this.$toast.success("修改成功")
       }else{
           this.$toast.fail("修改失败")
           this.mix_set_data("/address/list?page=1&limit=20", "addressData");
       }
       })

    },
    del_addr(id){
        this.axios.post("/address/del",{
           id
       }).then(data => {
        //    if(data.data.status)
       if(data.data.status == 200){
           this.$toast.success("删除成功")
       }else{
           this.$toast.fail("删除失败")
       }
           this.mix_set_data("/address/list?page=1&limit=20", "addressData");
       })
    },
    edit_addr(id){
        this.$router.push("/user/add_address/"+id);
    }
  },
  watch:{
      addressData(){
          this.addressData.forEach(v=>{
              if(v.is_default){
                  this.radio = v.id
              }
          })
      }
  }
};
</script>

<style lang="scss">
#app  >.use_add_manage {
  height: 100%;
  background-color: rgb(245, 245, 245);
  .addr{
      padding: 16px;
      margin-bottom: 5px;
      background-color: white;
      .top{
          .name{
              font-size: .9em;
              font-weight: bold;
              margin-bottom: 5px;
          }
          .address{
              font-size: .8em;
          }
          padding-bottom: 10px;
          margin-bottom: 10px;
          border-bottom: 1px solid silver;
      }
      .bottom  {
          display: flex;
          font-size: .9em;
          justify-content: space-between;
          align-items: center;
          .right{
              display: flex;
              align-items: center;
              .edit{
                  margin-right: 15px;
              }
          }
      }

  }
  .add{
      width: 100vw;
      position: fixed;
      bottom: 0;
      background-color:white;
      padding: 5px  16px;
      div{
          background-color: rgb(233,51,35);
          width: 100%;
          color: white;
          text-align: center;
          height: 2.6em;
          line-height: 2.6em;
         border-radius: 1.3em;
      }
  }
}
</style>