<template>
  <div class="use_add_address">
    <van-address-edit
      :area-list="areaList"
      show-set-default
      show-search-result
      :search-result="searchResult"
      
      :area-columns-placeholder="['请选择', '请选择', '请选择']"
      @save="onSave"

    ></van-address-edit>
  </div>
</template>

<script>
import areaList from "./../../assets/js/area";
export default {
  data() {
    return {
      searchResult: null,
      cityListData: null,
      areaList,
      oldAddrInfo: null,
    };
  },
  props: ["id"],
  created() {},
  methods: {
    onSave(v) {
      console.log(v);
      let id = this.id;
      let a = {
        address: {
          province: v.province,
          city: v.city,
          city_id: v.areaCode,
          district: v.county,
        },
        detail: v.addressDetail,
        is_default: v.isDefault? 1: 0,
        phone: v.tel,
        post_code: v.postalCode,
        real_name: v.name,
      };
      if (id.trim()) {
        a.id=this.id
      }
      console.log(a);
      this.axios.post("/address/edit",a).then(data => {
        if(data.data.status == 200){
          this.$toast.success("更改成功")
          this.$router.go(-1)
        }else{
          this.$toast.fail(data.data.msg)
        }
      })
    },
  },
};
</script>

