<template>
  <div class="user">
    <!-- 导航栏 -->
    <Tabbar></Tabbar>
    <div class="head">
      <div class="info">
        <div class="img">
          <img :src="user.avatar" />
        </div>
        <div class="account">
          <div class="name">{{ user.nickname }}</div>
          <div class="id">id：{{ user.uid }} <van-icon name="coupon-o" /></div>
        </div>
        <div class="set">
          <van-icon @click="$router.push(`/user/data`)" name="setting-o" />
        </div>
      </div>
    </div>
    <div class="rich">
      <div>
        <div @click="$router.push('/user/account')">
          <div>我的余额</div>
          <div>{{ user.now_money }}</div>
        </div>
        <div @click="$toast.fail('暂未开通')">
          <div >当前佣金</div>
          <div>{{ user.broken_commission }}</div>
        </div>
        <div @click="$router.push('/user/get_coupon')">
          <div>优惠券</div>
          <div>{{ user.couponCount }}</div>
        </div>
      </div>
    </div>
    <div class="pan1" @click="$router.push('/order/list')">
      <van-panel title="我的订单" status="全部订单 >">
        <div>
          <van-grid :column-num="5" :border="false">
            <van-grid-item :icon="icon.order[0]" text="代付款" />
            <van-grid-item :icon="icon.order[1]" text="代发货" />
            <van-grid-item :icon="icon.order[2]" text="待收货" />
            <van-grid-item :icon="icon.order[3]" text="待评价" />
            <van-grid-item :icon="icon.order[4]" text="售后" />
          </van-grid>
        </div>
      </van-panel>
    </div>
    <div class="pan2">
      <van-panel title="我的服务">
        <div>
          <van-grid :column-num="4" :border="false">
            <van-grid-item @click="$router.push('/user/vip')" :icon="icon.server[0]" text="会员中心" />
            <van-grid-item @click="$toast.fail('暂未开通')" :icon="icon.server[1]" text="砍价记录" />
            <van-grid-item @click="$toast.fail('暂未开通')" :icon="icon.server[2]" text="我的推广" />
            <van-grid-item @click="$router.push('/user/account')" :icon="icon.server[3]" text="我的余额" />
            <van-grid-item @click="$router.push('/user/add_manage')" :icon="icon.server[4]" text="地址信息" />
            <van-grid-item @click="$router.push('/collection')" :icon="icon.server[5]" text="我的收藏" />
            <van-grid-item @click="$router.push('/user/get_coupon')" :icon="icon.server[6]" text="优惠券" />
            <van-grid-item @click="$toast.fail('暂未开通')" :icon="icon.server[7]" text="联系客服" />
          </van-grid>
        </div>
      </van-panel>
    </div>
  </div>
</template>
<script>
import Tabbar from "./../components/Tabbar";
import icon from "./user.js";
export default {
  data() {
    return {
      icon: null,
    };
  },
  components: {
    Tabbar,
  },
  computed: {
    user: function () {
      return this.$store.state.user;
    },
  },
  created() {
    this.$store.commit("set_tabbarIndex", 3);
    this.icon = icon;
  },
  methods:{
    
  }
};
</script>
<style lang="scss">
$userred: rgb(233, 51, 35);
#app .user {
  background-color: rgb(245, 245, 245);
  height: 100%;
  width: 100%;
  .head {
    position: relative;
    box-sizing: border-box;

    .info {
      position: relative;
      display: flex;
      align-items: center;
      box-sizing: border-box;
      padding: 20px 16px;
      background-color: $userred;
      .img {
        width: 60px;
        height: 60px;
        background-color: blanchedalmond;
        border-radius: 50%;
        border: 1px solid white;

        img {
          width: 100%;
          height: 100%;
          display: block;
          border-radius: 50%;
        }
      }
      .account {
        margin-left: 10px;
        .name {
          color: white;
          font-size: 0.9em;
        }
        .id {
          color: hsla(0, 0%, 100%, 0.6);
          font-size: 0.8em;
          display: flex;
          align-items: center;
          i {
            margin-left: 5px;
          }
        }
      }
      .set {
        position: absolute;
        right: 30px;
        top: 30%;
        color: white;
      }
    }
    &::after {
      display: block;
      content: "";
      position: absolute;
      width: 100%;
      height: 50%;
      background-color: $userred;
      left: 0;
      bottom: -50%;
      border-bottom-left-radius: 100%;
      border-bottom-right-radius: 100%;
    }
  }
  .rich {
    position: relative;
    box-sizing: border-box;

    padding: 0 16px;
    > div {
      display: flex;
      background-color: white;
      border-radius: 5px;
      padding: 15px 0;
      > div {
        flex: 1;
        text-align: center;
        font-size: 0.9em;
        div:nth-child(1) {
          padding-bottom: 5px;
          color: rgb(170, 170, 170);
          font-size: 0.8em;
        }
      }
      > div:nth-child(2) {
        div {
          border-left: 1px solid rgb(170, 170, 170);
          border-right: 1px solid rgb(170, 170, 170);
        }
      }
    }
  }

  .pan1 {
    .van-cell__value {
      color: rgb(137, 137, 137);
      font-size: 0.9em;
    }
  }

  .pan1,
  .pan2 {
    .van-icon {
      color: palevioletred;
    }
    margin-top: 10px;
    box-sizing: border-box;
    padding: 0 16px;
    border-radius: 5px;
    .van-cell::after {
      border: 1px #ebedf0 dashed;
    }
  }
}
</style>