<template>
  <div class="act_goods_seckill">
    <div class="pan">
      <div class="red"></div>
      <div class="white"></div>
    </div>
    <div class="body">
      <van-tabs v-model="active"
      @change="val_change"
      animated
      >
        <van-tab v-for="(v,i) in indexData.seckillTime" :key="i"
       
        >
          <template #title>
            <div class="time">{{v.time}}</div>
            <span class="word">{{v.state}}</span>
          </template>
          <div class="content">
            <div v-if="hotData[i].content == undefined" align="center">
            <img src="http://47.115.51.185/h5/img/noGood.7aec7edd.png" alt=""
            style="width:50vw;margin:40px auto"
            >
          </div>
          </div>
          
        </van-tab>
        
        
      </van-tabs>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: 0,
      indexData:{},
      hotData:[]
    };
  },
  created(){
    this.mix_set_data("/seckill/index","indexData");
  },
  methods:{
   
    val_change(val){
     
      this.mix_set_data(`/seckill/list/${this.indexData.seckillTime[val].id}?page=1&limit=5","hotData[${this.indexData.seckillTime[val].id}]`)
      

    }
  },
  watch:{
    indexData(){
      this.active = this.indexData.seckillTimeIndex;

      this.indexData.seckillTime.forEach((v,i)=>[
        this.hotData[i] = {}
      ])
    }
  }
};
</script>

<style lang="scss">
#app > .act_goods_seckill {
  height: 100%;
  background-color: rgb(245, 245, 245);
  .pan {
    position: relative;
    .red {
      background-color: rgb(244, 73, 57);
      height: 100px;
      border-bottom-left-radius: 20px;
      border-bottom-right-radius: 20px;
    }
    .white {
      position: absolute;
      width: calc(100% - 32px);
      height: 80%;
      background-color: rgb(245, 245, 245);
      left: 16px;
      bottom: 0;
      border: 0.1px solid silver;
      border-radius: 10px;
    }
    padding-bottom: 50px;
  }
  .body{
      margin-top:20px ;
      .van-tab__text--ellipsis {
          -webkit-line-clamp: 2;
      }
      .van-tabs__line {
          display: none;
      }
      .time{
          font-weight: bold;
          font-size: 1.2em;
          color: black;
      }
      .word{
          font-size: .8em;
          text-align: center;
          color: black;
        //   padding: -.1em;
            padding: .1em .4em;
            border-radius: 1em;
      }
      .van-tab--active{
          .time{
              color: rgb(252,56,36) !important;
          }
          .word{
            background-color: rgb(252,56,36);
            color: white;
          }
      }
  }
}
</style>