<template>
  <div class="zhlogin">
    <van-form class="zhdl" @submit="login">
      <van-field
        v-model="username"
        name="account"
        left-icon="phone"
        placeholder="输入手机号码"
        :rules="[{ required: true }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="password"
        left-icon="lock"
        placeholder="输入登录密码"
        :rules="[{ required: true }]"
      />
      <div class="question">
        <span @click="set_tab(2)"><van-icon name="question-o" /> 忘记密码</span>
      </div>
      <div style="margin: 16px">
        <van-button class="btn" round block type="info" native-type="submit"
          >登录</van-button
        >
      </div>
      <div class="foot">
        没有账号?<span @click="set_tab(3)"> 立即注册</span>
      </div>
    </van-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: "",
      password: "",
    };
  },
  props: ["set_tab"],
  methods: {
    // 登录验证
    login(values) {
      this.$axios
        .post("/login", values)
        .then((data) => {
          if (data.data.status == 200) {
            this.$store.commit("set_token", data.data.data.token);
            console.log("成功获取到token");
            return this.$axios('/user');
          } 
          else {
            throw data.data.msg;
          }
        }).then(data =>{
          if(data.data.status == 200){
            console.log('成功获取用户数据',data);
            this.$store.commit("set_user", data.data.data);
            this.$router.push("/user");
          }else{
            throw data.data.msg;
          }
        })
        .catch((err) => {
          this.$toast.fail(err);
        });
    },
  },
};
</script>

<style lang="scss">
#app .login .zhlogin {
  input {
    text-indent: 1em;
  }
  .question {
    text-align: right;
    color: #666;
    margin-top: 10px;
    font-size: 0.9em;
    vertical-align: top;
  }
  .btn {
    height: 40px;
    background: linear-gradient(90deg, #f35447 0, #ff8e3c);
    border: none;
  }
  .foot {
    font-size: 0.9em;
    text-align: center;
    color: silver;
    span {
      color: red;
    }
  }
}
</style>


