<template>
  <div class="collection">
    <div class="col_list" v-if=" collectData.length > 0">
      <div class="shop" v-for="(v,i) in collectData" :key="i">
        <div class="img" :style="`background-image:url(${v.image})`"></div>
        <div class="info">
          <div class="top">
            <div class="van-ellipsis">
              {{v.store_name}}
            </div>
          </div>
          <div class="bottom">
            <div class="left">￥{{v.price}}</div>
            <div class="btn" @click="del(v.pid)">删除</div>
          </div>
        </div>
      </div>
    </div>
    <SquareShopList v-if=" collectData.length ==0" :data="hotData"></SquareShopList>
  </div>
</template>

<script>
import SquareShopList from "./../components/SquareShopList";
export default {
  data() {
    return {
      hotData: null,
      collectData: [],
    };
  },
  created() {
    this.mix_set_data("/product/hot?page=1&limit=20", "hotData");
    this.mix_set_data("/collect/user?page=1&limit=20", "collectData");
  },
  components: {
    SquareShopList,
  },
  methods:{
      del(id){
          this.axios.post("/collect/del",{
              category: "product",

id
          }).then(data => {
             if(data.data.status == 200){
                 this.$toast.success(data.data.msg)
    this.mix_set_data("/collect/user?page=1&limit=20", "collectData");

             }else{
                 this.$toast.fail(data.data.msg)
             }
          })
      }
  }
};
</script>

<style lang="scss">
#app > .collection {
  .col_list {
    .shop {
      padding: 16px;
      display: flex;
      .img {
        height: 70px;
        width: 70px;
        background-color: red;
        background-size: 100% 100%;
        border-radius: 5px;
      }
      .info {
        width: 60vw;
        box-sizing: border-box;
        padding-left: 10px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        flex: 1;
        .top {
          font-size: 0.8em;
        }
        .bottom {
          display: flex;
          justify-content: space-between;
          align-items: center;
          .left {
            color: rgb(252, 65, 65);
            font-size: 0.8em;
          }
          .btn {
            padding: 0 1em;
            border: 1px solid silver;
            font-size: 0.9em;
          }
        }
      }
    }
  }
}
</style>