<template>
  <div class="user_sign">
    <div class="head">
      <div class="littlecard"
      @click="$router.push('/user/sign_record')"
      >明细</div>
      <div class="img" :style="`background-image:url(${userData.avatar})`"></div>
      <div class="info">
        <div class="id">{{userData.nickname}}</div>
        <div class="integral">积分：{{ parseInt(userData.integral) }}</div>
      </div>
    </div>
    <div class="body">
      <div class="top">
        <div class="days">
          <div class="day" v-for="(v,i) in configData"
          :key="i"
          >
            <div class="tian">{{v.day}}</div>
            <div class="img " :class="i < userData.sign_num ? 'wty':'hty'" 
            
            ></div>
            <div class="fen">+{{v.sign_num}}</div>
          </div>
        </div>
        <div class="btn">
          <div v-if="!userData.is_day_sgin" @click="sign">立即签到</div>
          <div style="background-color:silver" v-if="userData.is_day_sgin"
        
          >已签到</div>
        </div>
      </div>
      <div class="lock"></div>
      <div class="bottom">
        <div class="title">已累计签到</div>
        <div class="day">
          <div class="card" v-for="(v,i) in sumSingDay.reverse()"
          :key="i"
          >{{v}}</div>
          <div>天</div>
        </div>
        <div class="word">
          据说连续签到第七天可获得超额积分，一定要坚持签到哦~~
        </div>
        <div class="signlist"  v-if="listData.length > 0">
          <div class="sign" v-for="(v,i) in listData"
          :key="i"
          >
            <div class="left">
              <div>{{v.title}}</div>
              <div>{{v.add_time}}</div>
            </div>
            <div class="right">+{{v.number}}</div>
          </div>
          <div class="more" @click="$router.push('/user/sign_record')">点击加载更多 ></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
    data () {
        return {
            configData:null,
            listData:[],
            userData:{
              integral:'',
              nickname:'',
              avatar:''
            },
        }
    },
    computed:{
      sumSingDay(){
       return [0,0,0,0].map((v,i)=>{
          return parseInt(this.userData.sum_sgin_day / Math.pow(10,i)) 
        })
      }
    },
    created(){
      // console.log(this.mix_set_data);
      this.mix_set_data("/sign/config","configData");
      this.mix_set_data("/sign/list?page=1&limit=3","listData");
      this.get_user_data();
    },
    methods:{
        get_user_data(){
          this.axios.post("/sign/user",{
            sign: 1
          }).then(data =>{
            if(data.data.status == 200){
              this.userData = data.data.data;
              console.log(this.userData);
            }
          })
        },
        sign(){
          this.axios.post("/sign/integral",{}).then(data => {
            if(data.data.status == 200){
               this.$dialog.alert({
              message: '签到成功',
              });
               this.mix_set_data("/sign/list?page=1&limit=3","listData");
            }else{
              this.$dialog.alert({
              message: '签到失败',
              });
            }
          })
        }
    }
};
</script>

<style lang="scss">
#app > .user_sign {
  background-color: rgb(245, 245, 245);
  width: 100%;
  height: 100%;
  .head {
    position: relative;
    background-color: rgb(233, 51, 35);
    padding: 2em 16px 1.5em 16px;

    .littlecard {
      position: absolute;
      right: 0;
      top: 20%;
      font-size: 0.8em;
      padding: 0.5em 0.3em 0.5em 1.6em;
      border-bottom-left-radius: 1.6em;
      border-top-left-radius: 1.6em;
      background-color: white;
      color: rgb(255, 164, 47);
    }
    display: flex;
    align-items: center;
    .img {
      width: 50px;
      height: 50px;
      background-size: 100% 100%;
      border-radius: 50%;
      background-color: white;
      margin-right: 10px;
    }
    .info {
      height: 3em;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .id {
        color: white;
        font-weight: bold;
        font-size: 0.9em;
      }
      .integral {
        font-size: 0.8em;
        background-color: rgb(255, 144, 0);
        color: rgb(255, 248, 189);
        padding: 0.2em 0.5em;
        border-radius: 0.5em;
      }
    }
    &::after {
      display: block;
      content: "";
      position: absolute;
      width: 100%;
      height: 2em;
      bottom: -2em;
      left: 0;
      z-index: 0;
      background-color: rgb(233, 51, 35);
    }
  }
  .body {
    position: relative;
    padding: 0 16px;
    .top {
      border-radius: 10px;
      background-color: white;
      padding: 20px 10px;
      .days {
        display: flex;
        .day {
          flex: 1;
          .tian,
          .fen {
            color: rgb(153, 155, 179);
            font-size: 0.8em;
          }
          .fen {
            color: rgb(255, 144, 0);
          }
          .img {
            margin: 3px 0;
            width: 25px;
            background-size: 100% 100%;
            height: 25px;
          }

          .wty {
            background-image: url(data:image/png;base64,);
          }
          .hty {
            background-image: url(data:image/png;base64,);
          }
          
        }
        .day:last-child{
          .tian{
            background-color: rgb(244,180,9);
           text-align: center;
           border-radius: 1em;
          }
          .img {
            background-image: url(data:image/png;base64,);
          }
        }
      }
      .btn {
        padding:20px 0;
        div {
          width: 60%;
          margin-left: 20%;
          display: flex;
          justify-content: center;
          align-items: center;
          background-color: rgb(233, 51, 35);
          color: white;
          height: 40px;
          border-radius: 20px;
        }
      }
    }
    .lock {
      height: 5px;
      position: relative;
      &::after {
        background-size: 100% 100%;
        content: "";
        position: absolute;
        display: block;
        width: 80%;
        left: 10%;
        background-image: url(data:image/png;base64,);
        height: 30px;
        top: -10px;
      }
    }
    .bottom {
      background-color: white;
      padding: 40px 16px;

      .title {
        color: dimgray;
        margin-bottom: 20px;
        text-align: center;
      }
      .day {
        display: flex;
        justify-content: center;
        align-items: flex-end;
        .card {
          width: 40px;
          color: white;
          line-height: 50px;
          text-align: center;
          position: relative;
          height: 50px;
          background-color: rgb(233, 51, 35);
          font-size: 40px;
          margin-right: 10px;
          &::after {
            content: "";
            position: absolute;
            width: 100%;
            display: block;
            height: 2px;
            background-color: rgba(255, 255, 255, 0.2);
            top: 50%;
          }
        }
      }
      .word {
        padding: 20px 30px;
        text-align: center;
        color: silver;
        font-size: 0.9em;
      }
      .signlist {
        border-top: 1px silver dotted;
        .sign {
          padding: 10px 0;
          display: flex;
          justify-content: space-between;
          align-items: center;
          border-bottom: 1px solid silver;
          .left {
            font-size: 0.9em;
            div:last-child {
              margin-top: 3px;
              color: silver;
            }
          }
          .right {
            color: red;
          }
        }
        .more {
          padding-top: 10px;
          text-align: center;
          font-size: 0.8em;
        }
      }
    }
  }
}
</style>