<template>
<div>
   <van-sku
      v-if="sku"
      v-model="show"
      :sku="sku"
      :goods="sku.goods"
      
      :hide-stock="sku.hide_stock"
      ref="vansku"
      :reset-selected-sku-on-hide="true"
    >
      <template #sku-actions>
        <div></div>
      </template>
    </van-sku>
    s
</div>
 
</template>

<script>
export default {
data () {
    return {
        sku:null,
        show:true,
detailData:null,
    }
  
  
},
created () {
        this.$axios("/product/detail/73").then((data) => {
        if (data.data.data) {
          this["detailData"] = data.data.data;
          let abc = data.data.data;
          let imgUrl = "";
          let list = [];
          let proObj = abc.productValue;
          for (let i in proObj) {
            imgUrl = proObj[i].image;
            let suk = proObj[i].suk.split(",");
            let aaa = {};
            for (let l = 0; l < suk.length; l++) {
              aaa["s" + (1 + l)] = suk[l];
            }
            let obj = {
              uniqueId: proObj[i].unique,
              id: proObj[i].product_id,
              price: proObj[i].price, //价格
              ...aaa,
              stock_num: proObj[i].stock, //库存
            };
            list.push(obj);
          }
          let tree = [];
          abc.productAttr.forEach((val, i) => {
            let v = val.attr_values.map((val1) => {
              return {
                name: val1,
                imgUrl: i == 0 ? imgUrl : undefined,
                id: val1,
              };
            });
            let obj = {
              k: val.attr_name,
              k_s: "s" + (i + 1),
              k_id: v.product_id,
              v,
            };
            tree.push(obj);
          });
          let a = {
            goods: {
              picture: abc.storeInfo.image,
            },
            tree,
            list,
            price: abc.storeInfo.price,
            stock_num: abc.storeInfo.stock,
            none_sku: false,
            hide_stock: false,
          };
          console.log(a);
          this.sku = a;
        }
        
        console.log(this.sku);
      });
    }
}
</script>

