import Vue from 'vue'
import './plugins/axios'
import App from './App.vue'
import router from './router'
import store from './store'

// 导入插件
import MyPlug from './plugins/MyPlug'
Vue.use(MyPlug);
import 'vant/lib/index.css';
import Vant  from 'vant';
import {
  Lazyload,Form,Tab, Tabs ,Tabbar, AddressList ,Area ,
  TabbarItem,Toast,IndexAnchor ,IndexBar,ActionSheet ,
  Stepper,Checkbox, CheckboxGroup,SubmitBar,Panel ,
  Grid, GridItem,GoodsAction, GoodsActionIcon, GoodsActionButton
  ,ShareSheet ,Sku, Col, Row,DropdownMenu, DropdownItem,Dialog 
  ,AddressEdit,Step, Steps
}  from 'vant';
Vue.use(Vant).use(Lazyload).use(Form).use(Tab).use(Sku )
.use(Tabs).use(Tabbar).use(TabbarItem).use(Toast).use(ShareSheet )
.use(IndexBar).use(IndexAnchor ).use(Stepper ).use(Checkbox).use(CheckboxGroup)
.use(SubmitBar).use(Panel ).use(Grid).use(GridItem).use(Col).use(Row)
.use(GoodsAction).use(GoodsActionIcon).use(GoodsActionButton)
