<template>
  <div class="user_data">
    <div class="head">
      <div class="title">管理我的账号</div>
      <div class="info">
        <div class="littlecard">当前账号</div>
        <label for="user_data_upload">
          <div class="img" :style="`background-image:url(${setData.avatar})`">
            <van-icon name="edit" />
          </div>
        </label>
        <input
          type="file"
          @change="upload_image"
          id="user_data_upload"
          accept="image/gif, image/jpeg, image/png, image/jpg"
        />
        <div class="phone">
          <div class="name">{{ setData.nickname }}</div>
          <div class="number">绑定手机号：{{ setData.phone }}</div>
        </div>
      </div>
    </div>
    <div class="body">
      <div>
        <div class="left">昵称</div>
        <div class="right">{{ setData.nickname }}</div>
      </div>
      <div>
        <div class="left">ID号</div>
        <div class="right">{{ setData.uid }} <van-icon name="warn-o" /></div>
      </div>
      <div>
        <div class="left">手机号码</div>
        <div class="right">{{ setData.phone }} <van-icon name="warn-o" /></div>
      </div>
      <div>
        <div class="left">密码</div>
        <div class="right" @click="$router.push(`/change_password`)">
          点击修改密码 {{ `>` }}
        </div>
      </div>
    </div>
    <div class="btns">
      <div class="btn1" @click="save_edit">保存修改</div>
      <div class="btn2" @click="logout">退出登录</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      uploadData: null,
    };
  },
  computed: {
    setData() {
      return this.$store.state.user;
    },
  },
  created() {},
  mounted() {
    // 给input绑定事件
    this.upload_image();
  },
  methods: {
    upload_image(e) {
      if (e) {
        this.$toast.fail("上传失败");
      }
    },
    save_edit() {
      this.axios
        .post("/user/edit", {
          avatar: this.setData.avatar,
          nickname: this.setData.nickname,
        })
        .then((data) => {
          if (data.data.status == 200) {
            this.$toast.success("修改成功");
            this.$router.go(-1);
          } else {
            this.$toast.fail(this.data.msg);
          }
        });
    },
    beforeClose(action, done) {
      
      if (action === "confirm") {
        this.axios("/logout").then(data =>{
          if(data.data.status == 200){
             window.localStorage.clear();
             window.location.reload();
          }else{
            done();
          }
        })
      } else {
        done();
      }
    },
    logout() {
      this.$dialog.confirm({
        title: "提示",
        message: "确认退出登录",
        beforeClose:this.beforeClose,
      });
    },
  },
};
</script>

<style lang="scss">
#app > .user_data {
  background-color: rgb(245, 245, 245);
  width: 100%;
  height: 100%;
  .head,
  .body {
    background-color: white;
    box-sizing: border-box;
    padding: 0 16px;
  }
  .head {
    input {
      display: none;
    }
    margin-bottom: 10px;
    padding-bottom: 20px;
    .title {
      padding-top: 20px;
      margin-bottom: 20px;
    }
    .info {
      border: 1px solid rgb(239, 106, 95);
      position: relative;
      border-radius: 10px;
      overflow: hidden;
      background-image: url("data:image/png;base64,");
      background-size: 100% 100%;
      padding: 15px;
      display: flex;
      align-items: center;
      .littlecard {
        position: absolute;
        top: 0;
        right: 0;
        background-color: rgb(253, 229, 228);
        padding: 5px;
        border-bottom-left-radius: 10px;
        font-size: 0.5em;
        color: rgb(252, 65, 65);
      }
      .img {
        background-size: 100% 100%;
        width: 50px;
        height: 50px;
        position: relative;
        border-radius: 50%;
        margin-right: 10px;
        .van-icon {
          position: absolute;
          right: 0;
          bottom: 0;
          font-size: 0.8em;
          color: red;
          font-weight: bold;
          background-color: white;
          border-radius: 50%;
          border: 1px solid black;
          padding: 0.5px;
        }
      }
      .phone {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 40px;
        .name {
          font-size: 0.9em;
        }
        .number {
          font-size: 0.8em;
          color: rgb(166, 153, 177);
        }
      }
    }
  }
  .body {
    margin-bottom: 40px;
    > div {
      border-bottom: 1px solid rgb(245, 245, 245);
      padding: 15px 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .right {
        color: rgb(166, 153, 177);
        font-size: 0.9em;
      }
    }
  }
  .btns {
    padding: 0 20px;
    .btn1,
    .btn2 {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 44px;
      border-radius: 22px;
    }
    .btn1 {
      background-color: rgb(233, 51, 35);
      color: white;
      margin-bottom: 15px;
    }
    .btn2 {
      border: 1px solid rgb(233, 51, 35);
      color: rgb(233, 51, 35);
    }
  }
}
</style>