<template>
    <div class="register">
        <van-form @submit="login">
              <van-field
                v-model="username"
                name="account"
                 left-icon="phone"
                placeholder="输入手机号码"
                :rules="[{ required: true,  }]"
              />
              <van-field
                v-model="yzm"
                type="number"
                name="captcha"
               left-icon="lock"
                placeholder="输入验证码"
                :rules="[{ required: true,  }]"
              >
              <template #right-icon>
                  <div class="code" @click="get_code">{{code}}</div>
              </template>
              </van-field>
                <van-field
                v-model="password"
                name="password"
                type="password"
                 left-icon="lock"
                placeholder="输入登录密码"
                :rules="[{ required: true,  }]"
              />
              <div style="margin: 16px">
                <van-button class="btn" round block type="info" native-type="submit"
                  >注册</van-button
                >
              </div>
              <div class="foot">没有账号?<span @click="set_tab(1)">立即登录</span></div>
            </van-form>
    </div>
</template>
<script>

export default {
  data() {
    return {
      yzm:"",
      username: "",
      password: "",
       code:"获取验证码",
      // 获取验证码按钮是否可用，
      codebtn:true,
    };
  },
  props:['set_tab'],
  methods: {
    // 登录验证
    login(values) {
    //  console.log(values.password);
    if(values.password.length > 5 ){
      this.$axios.post("/register",values).then(data => {
        console.log(data);
        if(data.data.status == 200){
          this.$toast.success(data.data.msg);
          this.set_tab(1);
        }else{
          this.$toast.fail(data.data.msg)
        }
      })
    }else{
      this.$toast.fail("密码少于六位")
    }
    },
    // 获取验证码
    get_code:function(){
      // console.log('get_code');
      let key = this.$store.state.key;
      // 测试号码对不对
      if(/1\d{10}/.test(this.username) && this.codebtn){
        this.codebtn = false;
        let num = 61 ;
      let time = null;
      time = setInterval(() => {
        num--;
        this.code =  `还剩${num < 10 ? '0'+num  : num}秒`;
        if(num < 1){
          clearInterval(time);
          this.code = "获取验证码";
          this.codebtn = true;
        }
      }, 1000);

        // Toast.success('成功文案');
        this.$axios.post('/register/verify',{key,phone:this.username,type:'register'})
        .then(data => {
          if(data.data.status == 200){
            this.$toast.success(data.data.msg);
          }else{
            throw data.data.msg;
          }
        })
        .catch((err)=>{
          this.$toast.fail(err);
        })
      }else{
        this.$toast.fail('手机号码错误');
        
      }
      
    }
  },
};
</script>
<style lang="scss">
#app .login .register{
  input {
    text-indent: 1em;
  }
  
  .btn{
    height: 40px;
    background: linear-gradient(90deg,#f35447 0,#ff8e3c);
    border: none;
    font-size: 1.1em;
    font-weight: bold;
  }
  .foot{
    font-size: .9em;
    text-align: center;
    color: silver;
    span{
      color: red;
    }
    
  }
  .code{
        background-color: rgb(243,84,70);
        color:white;
        padding: 0 .5em;
        font-size: .6em;
        border-radius: 2em;
        
    }
}
</style>

