<template>
  <div class="ord_status" v-if="detailData">
    <div class="box">
      <div class="err">
        <span class="fa-stack fa-lg">
          <i class="fa fa-circle fa-stack-2x"></i>
          <i class="fa fa-close fa-stack-1x fa-inverse"></i>
        </span>
      </div>
      <div class="head">订单支付失败</div>
      <div class="infolist">
        <div class="info">
          <div>订单编号</div>
          <div>{{ detailData.order_id }}</div>
        </div>
        <div class="info">
          <div>下单时间</div>
          <div>{{ detailData.add_time_y }} {{ detailData.add_time_h }}</div>
        </div>
        <div class="info">
          <div>支付方式</div>
          <div>
            {{ detailData.pay_type == "yue" ? "余额支付" : "H5微信支付" }}
          </div>
        </div>
        <div class="info">
          <div>支付金额</div>
          <div>{{ detailData.pay_price }}</div>
        </div>
      </div>
      <div class="btns">
        <div class="btn1" @click="$router.replace({name:`ordDetail`,params:detailData})">查看订单</div>
        <div class="btn2" @click="$router.replace('/')">返回首页</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  // 数据
  data() {
    return {
      detailData:null
    };
  },
  // 生命周期
  created() {
    
    console.log(this.$route);
    let key = this.$route.query.key;
    let postArgument = this.$route.params;
    console.log(postArgument);
    this.axios.post("/order/create/"+key,postArgument,postArgument).then(d =>{
        this.$toast.fail(d.data.msg);
        this.axios('/order/detail/'+d.data.data.result.orderId).then(d1 => {
            console.log(d1);
            this.detailData = d1.data.data;
        })
    })
  },
  // 其他
};
</script>

<style lang="scss">
#app > .ord_status {
  background-color: #f5f5f5;
  height: 100%;
  padding: 0 16px;
  overflow: auto;
  .box {
    border-radius: 5px;
    background-color: white;
    padding: 0 16px;
    padding-top: 40px;
    margin-top: 20%;
    position: relative;
    .err {
      position: absolute;
      left: 50%;
      transform: translate(-50% ,-50%);
      top: 0;
      span{
          color: #999999;
          transform: scale(1.5);
          i{
              text-shadow: 0 4px 0 #7a7a7a;
          }
      }
    }
    .head {
      padding-bottom: 20px;
      border-bottom: #eeeeee 1px solid;
      text-align: center;
      font-weight: bold;
    }
    .infolist {
      padding-top: 16px;
      border-bottom: 1px solid #eee;
      .info {
        display: flex;
        justify-content: space-between;
        margin-bottom: 16px;
        font-size: 0.9em;
      }
      div:nth-child(2) {
        color: #666666;
        font-size: 0.9em;
      }
    }
    .btns {
      padding: 16px 0;
      div {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #e93323;
        border: 1px solid #e93323;
        color: white;
        height: 2.5em;
        border-radius: 1.25em;
      }
      div:nth-child(2) {
        margin-top: 10px;
        color: #e93323;
        background-color: white;
      }
    }
  }
}
</style>