<template>
  <div class="ord_detail">
    <div class="status">
      <div class="left"></div>
      <div class="right">
        <div class="top">{{detailData._status._msg}}</div>
        <div class="bottom">{{detailData._pay_time}}</div>
      </div>
    </div>
    <div class="step">
      <van-steps active-color="#fc4141" :active="0">
        <van-step>代付款</van-step>
        <van-step>代发货</van-step>
        <van-step>待收货</van-step>
        <van-step>待评价</van-step>
        <van-step>已完成</van-step>
      </van-steps>
    </div>
    <div class="addr">
      <div class="top">
        {{detailData.real_name}}  {{detailData.user_phone}}
      </div>
      <div class="bottom">
        {{detailData.user_address}}
      </div>
    </div>
    <div class="shoplist">
      <div class="sum">共{{detailData.cartInfo.length}}件商品</div>
      <div class="shop" v-for="(v,i) in detailData.cartInfo" :key="i">
        <div
          class="img"
          :style="`background-image:url(${v.productInfo.attrInfo.image})`"
        ></div>
        <div class="info">
          <div class="name van-ellipsis">{{v.productInfo.store_name}}</div>
          <div class="sku">{{v.productInfo.attrInfo.suk}}</div>
          <div class="price">￥{{v.productInfo.attrInfo.price}}</div>
        </div>
        <div class="num">x {{v.cart_num}}</div>
      </div>
      <div class="connect" @click="$toast.fail('暂未开通此功能')"><van-icon name="service-o" /> 联系客户</div>
    </div>
    <div class="shopinfo">
      <div>
        <div>订单编号：</div>
        <div @click="$toast.fail('复制失败')"> {{detailData.order_id}}<span>复制</span></div>
      </div>
      <div>
        <div>下单时间：</div>
        <div>{{detailData._pay_time}}</div>
      </div>
      <div>
        <div>订单类型：</div>
        <div>普通订单</div>
      </div>
      <div>
        <div>支付状态：</div>
        <div>未支付</div>
      </div>
      <div>
        <div>支付方式：</div>
        <div>{{detailData._status._payType}}</div>
      </div>
      <div v-if="detailData.mark">
        <div>买家留言：</div>
        <div>{{detailData.mark}}</div>
      </div>
    </div>
    <div class="shopinfo payinfo">
      <div>
        <div>支付金额：</div>
        <div>￥{{detailData.total_price}}</div>
      </div>
      <div v-if="detailData.deduction_price > 0">
        <div>积分抵扣：</div>
        <div>-{{detailData.deduction_price}}</div>
      </div>
      <div v-if="detailData.pay_postage > 0">
        <div>运费：</div>
        <div>+{{detailData.pay_postage}}</div>
      </div>
    </div>
    <div class="pay">实付款: <span>￥{{detailData.pay_price}}</span></div>
    <div class="paybtns">
      <div class="btn1" @click="cancal">取消订单</div>
      <div class="btn2" @click="show=true">立即付款</div>
    </div>
    <van-action-sheet
    title="选择付款方式"
     v-model="show" :actions="actions" @select="onSelect" />
  </div>
</template>

<script>
export default {
  data() {
    return {
      detailData: null,
      show:false,
       actions: [{ name: '微信支付',subname:'使用微信快捷支付',paytype:"weixin" }, {paytype:"yue", name: '余额支付',subname:`使用当前用户余额支付`}],

    };
  },
  created() {
    this.detailData = this.$route.params;
    console.log(this.detailData);
  },
  methods:{
    onSelect(item) {
      // 默认情况下点击选项时不会自动收起
      // 可以通过 close-on-click-action 属性开启自动收起
      // this.show = false;
      // this.$toast(item.name);
      // console.log(item);
      this.show = false;
      this.axios.post("/order/pay",{from:"weixinh5",paytype:item.paytype,uni:this.detailData.order_id}).then(data =>{
        if(data.data.status == 200){
          this.$toast.success(data.data.msg)
        }else{
          this.$toast.fail(data.data.msg)
        }
      })
    },
    cancal() {
      let id = this.detailData.order_id;
      this.$axios.post("/order/cancel", { id }).then((data) => {
        if (data.data.status == 200) {
          this.$router.replace("/order/list/")
          this.$toast.success(data.data.msg);
          
        } else {
          this.$toast.fail(data.data.msg);
        }
      });
    },
  }
};
</script>

<style lang="scss">
#app > .ord_detail {
  padding-bottom: 60px;
  height: 100%;
  overflow: auto;
  background-color: #f5f5f5;
  .status {
    display: flex;
    padding: 16px;
    background-color: #e93323;
    color: white;
    align-items: center;
    .left {
      width: 60px;
      height: 60px;
      background-size: 100% 100%;
     background-image: url(http://datong.crmeb.net/public/uploads/attach/2019/03/28/5c9ccca151e99.gif);
    }
    .right {
      padding-left: 10px;
      font-size: 0.9em;
      .top {
        font-weight: bold;
        padding-bottom: 5px;
      }
      .bottom {
        font-size: 0.8em;
      }
    }
  }
  .step {
    background-color: white;
    > div {
      overflow: visible;
    }
    .van-hairline:first-child {
      > div:nth-child(1){
        transform: translateX(-10px);
      }
    }
    .van-hairline:last-child {
      > div:nth-child(1){
        transform: translateX(10px);
      }
    }
    .van-step__title {
      font-size: 1.1em;
    }
    padding: 16px 20px;
    padding-bottom: 10px;
  }
  .addr{
    margin-bottom: 10px;
    background-color: white;
    padding: 0 16px;
    padding-bottom: 16px;
    .top{
      font-size: .9em;
    }
    .bottom{
      padding-top: .5em;
      color: #868686;
      font-size: .8em;
    }
  }
  .shoplist {
    background-color: white;
    .sum {
      padding: 16px;
      border-bottom: 2px solid #f5f5f5;
      font-size: 0.9em;
    }
    .shop {
      position: relative;
      display: flex;
      padding: 16px;
      border-bottom: 1px solid #f5f5f5;
      .img {
        width: 70px;
        height: 70px;
        background-color: violet;
        border-radius: 5px;
        background-size: 100% 100%;
      }
      .info {
        position: relative;
        flex: 1;
        min-width: 0;
        padding: 0 16px;
        font-size: 0.9em;
        .sku {
          font-size: 0.9em;
          margin-top: 3px;
        }
        .price {
          position: absolute;
          bottom: 0;
          left: 16px;
          color: red;
          font-size: 0.8em;
        }
      }
      .num {
        width: 2em;
        font-size: 0.8em;
        color: #968686;
      }
    }
    .connect{
      padding: 10px;
      text-align: center;
      font-size: .9em;
      color: #e93323;
    }
  }
  .shopinfo{
    margin: 10px 0;
    background-color: white;
    padding: 0 16px;
    padding-bottom: 10px;
    > div{
      span{
        color: black;
        border: 1px solid black;
        padding: .1em .4em;
        margin-left: 16px;
        font-weight: 100;
      }
      display: flex;
      padding-top: 10px;
      justify-content: space-between;
      align-items: center;
      font-size: .9em;
      >div:last-child{
        font-size: .9em;
        color:#868686 ;
      }
    }
  }
  .payinfo{
    margin-bottom: 0;
    padding-bottom: 0;
    >div:last-child{
     
      border-bottom: 1px solid #f1f1f1;
      padding-bottom: 10px;
    }
  }
  .pay{
    padding: 16px;
    text-align: right;
    background-color: white;
    font-size: .9em;
    span{
      color: #fc4141;
      font-weight: bold;
    }
  }
  .paybtns{
    height: 50px;
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    background-color: white;
    padding: 0 16px;
    width: 100%;
    div{
      border: 1px solid transparent;
      padding: .2em .5em;
      border-radius: 1em;
      font-size: .9em;
    }
    .btn1{
      color:#aaa ;
      border-color: #aaaaaa;
      margin-right: .5em;
    }
    .btn2{
      background-color: rgb(233,51,35);
      color: white;
    }
  }
}
</style>