<template>
  <div class="cart">
    <!-- 导航栏 -->

    <Tabbar></Tabbar>
    <!-- 商品管理 -->
    <div class="setshopbar">
      <div class="left">
        购物数量 <span>{{ countData.count > 0 ? countData.count : 0  }}</span>
      </div>
      <div class="right" v-if="countData.count != 0" @click="isShow = !isShow">
        {{ isShow ? "管理" : "取消" }}
      </div>
    </div>
    <!-- 当购物车有商品时 -->
    <div class="shopshow" v-if="countData.count > 0">
      <van-checkbox-group v-model="result" ref="checkboxGroup">
        <div class="shop" v-for="(v, i) in listData.valid" :key="i + 'abcd'">
          <!-- 勾选 -->
          <van-checkbox
            :name="i"
            @click="rad_change"
            checked-color="#ee0a24"
          ></van-checkbox>
          <van-card
            :price="v.truePrice"
            :desc="`属性：${v.productInfo.attrInfo.suk}`"
          >
            <template #title>
              <div class="van-ellipsis">{{ v.productInfo.store_name }}</div>
            </template>
            <template #num>
              <!-- 数量 -->
              <van-stepper
                @change="numchange(shopListNum[i], v.id)"
                v-model="shopListNum[i]"
              />
            </template>
            <template #thumb>
              <div class="img">
                <img :src="v.productInfo.image" />
              </div>
            </template>
          </van-card>
        </div>
      </van-checkbox-group>

      <div class="subbar">
        <van-submit-bar v-if="isShow" @submit="submit_order" :price="sum * 100" button-text="提交订单">
          <van-checkbox
            checked-color="#ee0a24"
            @click="checkAll"
            v-model="isSelect"
            >全选({{ shopNum }})</van-checkbox
          >
        </van-submit-bar>
        <van-submit-bar v-if="!isShow">
          <van-checkbox checked-color="#ee0a24" v-model="isSelect"
            >全选({{ shopNum }})</van-checkbox
          >
          <template #button>
            <div>
              <van-button plain round @click="collect" type="danger"
                >收藏</van-button
              >
              <van-button plain round @click="del" type="default"
                >删除</van-button
              >
            </div>
          </template>
        </van-submit-bar>
      </div>
    </div>
    <!-- 当购物车无商品时 -->
      <div class="hotshow" v-if="countData.count == 0">
          <SquareShopList
          :data="hotData"
          :isshow="true"
          ></SquareShopList>
      </div>
      <div class="loading" v-if="countData.count == -1"  >
       <div data-loader="ball-auto" style="position: fixed; left: 45%; top: 45%;"></div>
      
      </div>
  </div>
</template>
<style lang="scss">
#app > .cart {
  height: 100%;
  width: 100%;

  background-color: rgb(245, 245, 245);
  .setshopbar {
    box-sizing: border-box;
    padding: 5px 14px;
    display: flex;
    background-color: white;
    justify-content: space-between;
    align-items: center;
    .left {
      height: 2em;
      line-height: 2em;
      font-size: 0.9em;
      span {
        font-size: 0.8em;
        color: red;
      }
    }
    .right {
      font-size: 0.9em;
      border: 1px solid #868686;
      padding: 2px 8px;
      border-radius: 3px;
      color: #282828;
    }
  }
  .shopshow {
    .shop {
      position: relative;
      .van-checkbox {
        position: absolute;
        z-index: 99;
        left: 20px;
        top: 40%;
      }
      .van-card {
        background-color: white;
        padding-left: 50px;
        .img {
          height: 100%;
          width: 100%;
          img {
            display: block;
            height: 100%;
            width: 100%;
          }
        }
      }
    }
    .subbar {
      .van-submit-bar {
        padding: 10px 0;
        bottom: 50px;
        .van-submit-bar__bar {
          justify-content: space-between;
        }
      }
      .van-submit-bar__bar {
        height: 30px;
      }
      .van-button--round {
        height: 30px;
        width: 90px;
        margin-left: 10px;
      }
    }
  }
  .hotshow {
    position: relative;
    box-sizing: border-box;
    background-color: white;
    margin-top: 20px;
    
  }
}
</style>
<script>
import Tabbar from "./../components/Tabbar";
import SquareShopList from "./../components/SquareShopList";
import 'css-loading'
// import 'loading-css'

export default {
  data() {
    return {
      listData: {
        valid: [
          {
            cart_num: 0,
            id: 0,
            truePrice: 0,
            productInfo: {
              attrInfo: {
                suk: "",
              },
              store_name: "",
              image: "",
            },
          },
        ],
      },
      countData: { count: -1 },
      hotData: null,
      isShow: true,
      // isSelect:false,
      // 总值
      sum: 0,
      shopNum: 0,
      isSelect: false,
      result: [],
      // numArr:[],
      // numArr[i]
    };
  },
  computed: {
    shopListNum: function () {
      return this.listData.valid.map((item) => {
        return item.cart_num;
      });
    },
    shopListPrice: function () {
      return this.listData.valid.map((item) => {
        return item.truePrice;
      });
    },
  },
  components: {
    Tabbar,
    SquareShopList
  },
  created() {
    // 定向导航栏
    this.$store.commit("set_tabbarIndex", 2);
    // 获取数据
    // 获取推荐商品

    this.get_data("/cart/list", "listData");
    this.get_data("/product/hot?page=1&limit=20", "hotData");
    this.get_data("/cart/count", "countData");
  },
  methods: {
    // 获取数据
    get_data: function (url, obj) {
      this.$axios(url).then((data) => {
        if (data.data.status == 200) {
          this[obj] = data.data.data;
          console.log(this[obj]);
        }
      });
    },
    // 计算总价
    calcul_sum: function () {
      let newSelect = 0;
      let shopNum = 0;
      for (let i = 0; i < this.result.length; i++) {
        newSelect +=
          this.shopListNum[this.result[i]] * this.shopListPrice[this.result[i]];
        shopNum += this.shopListNum[this.result[i]];
      }
      this.sum = newSelect;
      this.shopNum = shopNum;
    },
    // 商品数量改变
    numchange: function (number, id) {
      this.calcul_sum();
      this.$axios.post("/cart/num", { number, id }).then((data) => {
        console.log(data);
      });
    },
    rad_change: function () {
      this.calcul_sum();
      if (this.result.length == 0) {
        this.isSelect = false;
      } else {
        this.isSelect = true;
      }
    },
    // 全选框
    checkAll: function () {
      this.$refs.checkboxGroup.toggleAll(this.isSelect);
      this.calcul_sum();
    },
    // 收藏
    collect: function () {
      let cObj = {
        category: "product",
        id: this.result.map((item) => {
          return this.listData.valid[item].id;
        }),
      };
      this.$axios.post("/collect/all", cObj).then((data) => {
        console.log(data);
      });
    },
    del: function () {
      let cObj = {
        ids: this.result.map((item) => {
          return this.listData.valid[item].id;
        }),
      };
      this.$axios.post("/cart/del", cObj).then((data) => {
        console.log(data);
        this.get_data("/cart/list", "listData");
        this.get_data("/cart/count", "countData");
      });
    },
    submit_order(){
      // console.log('submit');
      // console.log(this.result);
      // console.log(this.listData.valid);
      let str = [];
      this.result.forEach(v=>{
        str.push(this.listData.valid[v].id) 
      });
      // console.log(str);
      if(str.length >0 ){
        this.$router.replace("/order/submit/"+str.toString())
      }
    }
  },
  watch: {},
};
</script>