import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token:null,
    key:null,
    // 首页数据
    index:null,
    // 用户数据
    user:null,
    // 分类数据
    category:null,
    // 购物车数据
    cart:null,
    // 导航栏下标
    tabbarIndex:0,
   

  },
  getters:{
    isToken:function(state){
      if(state.token != null && state.user != null){
        return true;
      }else{
        return false;
      }
    }
  },
  mutations: {
    set_token:function(state,c){
      state.token = c;
      // 保存到本地
      // console.log(c);
      window.localStorage.setItem("dxpshop", c);
    },
    set_user:function(state,c){
      state.user = c;
    }
    ,set_index:function(state,c){
      state.index = c;
    }
    ,set_category:function(state,c){
      state.category = c;
    }
    ,set_key:function(state,c){
      state.key = c;
    }
    ,set_tabbarIndex:function(state,c){
      state.tabbarIndex = c;
    }
  },
  actions: {
  },
  modules: {
  }
})
