<template>
  <div class="ord_submit" v-if="confirmData && addrListData">
    <div class="head">
      <div class="box">
        <div class="top">
          <div class="left">快递配送</div>
          <div @click="$toast.fail('暂无门店信息')" class="right">到店自提</div>
        </div>
        <div class="bottom">
          <div class="addr" v-if="!confirmData.addressInfo">
            <div class="left">设置收货地址</div>
            <div class="right">
              <van-icon @click="select_addr(0)" name="arrow" />
            </div>
          </div>
          <template v-else>
            <div
              v-for="(v, i) in addrListData"
              :key="i"
              v-show="v.id == payloadObj.addressId"
            >
              <div class="name van-ellipsis">
                {{ v.real_name }}
                <span style="width: 1em; display: inline-block"></span>
                {{ v.phone }}
              </div>
              <div class="addr2 van-ellipsis">
                <span v-if="v.is_default">[默认]</span>
                {{ v.province + v.city + v.detail + v.district }}
              </div>

              <div class="arrow">
                <van-icon @click="select_addr(1)" name="arrow" />
              </div>
            </div>
          </template>
          <van-action-sheet
            cancel-text="新加地址"
            v-model="addrSelectShow"
            title="选择地址"
            :closeable="false"
            @cancel="$router.push('/user/add_address/ ')"
          >
            <div class="content1" v-if="addrListData.length > 0">
              <div class="addrbox">
                <div
                  @click="set_addr(v.id)"
                  class="addr1"
                  v-for="(v, i) in addrListData"
                  :key="i"
                  :class="{ addrselect: v.id == payloadObj.addressId }"
                >
                  <div class="left fa fa-map-marker"></div>
                  <div class="right">
                    <div class="name">
                      {{ v.real_name }}
                      <span style="width: 0.5em; display: inline-block"></span>
                      {{ v.phone }}
                    </div>
                    <div class="info">
                      {{ v.province + v.city + v.detail + v.district }}
                    </div>
                  </div>
                  <div class="fa fa-check"></div>
                </div>
              </div>
            </div>
            <div class="content2" v-else>
              <van-empty image="error" description="暂无添加地址" />
            </div>
          </van-action-sheet>
        </div>
      </div>
    </div>
    <div class="shoplist">
      <div class="sum">共{{ confirmData.cartInfo.length }}件商品</div>
      <div class="shop" v-for="(v, i) in confirmData.cartInfo" :key="i">
        <div
          class="img"
          :style="`background-image:url(${v.productInfo.image})`"
        ></div>
        <div class="info">
          <div class="name van-ellipsis">
            {{ v.productInfo.store_name }}
          </div>
          <div class="sku">{{ v.productInfo.attrInfo.suk }}</div>
          <div class="price">￥{{ v.productInfo.price }}</div>
        </div>
        <div class="num">x {{ v.cart_num }}</div>
      </div>
    </div>
    <div class="body">
      <div>
        <div class="left">优惠劵</div>
        <div @click="couponShow = true" class="right">
          请选择 <van-icon name="arrow"></van-icon>
        </div>
      </div>
      <van-action-sheet v-model="couponShow" title="优惠券">
        <div class="content"><van-empty description="暂无优惠劵可用" /></div>
      </van-action-sheet>
      <div>
        <div class="left">积分抵扣</div>
        <div class="right jifen">
          当前积分 <span>{{ $store.state.user.integral }}</span>
          <van-checkbox v-model="checked"></van-checkbox>
        </div>
      </div>
      <div>
        <div class="left">快递费用</div>
        <div class="right">
          
            {{
              priceList.pay_postage > 0
                ? `￥${priceList.pay_postage}`
                : "免运费"
            }}
          
        </div>
      </div>
      <div class="beizhu">
        <div class="left">备注信息</div>
        <div class="right">
          <van-field
            v-model="message"
            rows="2"
            autosize
            type="textarea"
            placeholder="请添加备注（150字以内）"
            show-word-limit
          />
        </div>
      </div>
    </div>
    <div class="pay">
      <div class="title">支付方式</div>
      <label class="box" :class="{ select: pay == 'pay1' }" for="pay1">
        <div class="left">
          <span
            class="fa-stack fa-lg"
            style="color: #41b035; transform: scale(0.6)"
          >
            <i class="fa fa-circle fa-stack-2x"></i>
            <i class="fa fa-weixin fa-stack-1x fa-inverse"></i>
          </span>
          微信支付
        </div>
        <div class="right">微信快捷支付</div>
      </label>
      <label :class="{ select: pay == 'pay2' }" class="box" for="pay2">
        <div class="left">
          <span
            class="fa-stack fa-lg"
            style="color: #fe960f; transform: scale(0.6)"
          >
            <i class="fa fa-circle fa-stack-2x"></i>
            <i class="fa fa-rmb fa-stack-1x fa-inverse"></i> </span
          >余额支付
        </div>
        <div class="right">可用余额：0.00</div>
      </label>

      <input type="radio" v-model="pay" name="pay" value="pay1" id="pay1" />
      <input
        type="radio"
        checked
        v-model="pay"
        value="pay2"
        name="pay"
        id="pay2"
      />
    </div>
    <div class="sumprice">
      <div>
        <div>商品总价：</div>
        <div class="right">￥{{ priceList.total_price }}</div>
      </div>
      <div v-if="priceList.deduction_price > 0">
        <div>积分抵扣：</div>
        <div class="right">-￥{{ priceList.deduction_price }}</div>
      </div>
      <div v-if="priceList.pay_postage > 0">
        <div>运费：</div>
        <div class="right">+￥{{ priceList.pay_postage }}</div>
      </div>
    </div>
    <div class="submit">
      <van-submit-bar
        :price="priceList.pay_price * 100"
        button-text="立即结算"
        @submit="onSubmit"
      />
    </div>
  </div>
</template>

<script>
export default {
  // 数据
  data() {
    return {
      // 支付方式
      pay: "pay2",
      // 是否选择积分
      checked: "",
      // 备注信息
      message: "",
      // 基本数据
      confirmData: null,
      // 地址列表
      addrListData: [],
      // 是否显示选择地址列表
      addrSelectShow: false,
      // 更新码
      orderKey: null,
      // 请求参数
      payloadObj: {
        couponId: 0,
        payType: "yue",
        shipping_type: 1,
        useIntegral: 0,
      },
      // 总价格
      priceList: {
        SurplusIntegral: 0,
        coupon_price: 0,
        deduction_price: 0,
        pay_postage: 0,
        pay_price: 400,
        total_price: 0,
      },
      couponShow: false,
    };
  },
  // 生命周期
  created() {
    this.get_confirm();
    this.mix_set_data("/address/list", "addrListData");
  },
  // 其他
  methods: {
    onSubmit() {
      console.log("submit");
      if (!this.payloadObj.addressId) {
        this.$toast.fail("请添加地址");
        return false;
      }
      this.$router.replace({
        name: "ordStatus",
        params: {
          real_name: "",
          phone: "",
          addressId: this.payloadObj.addressId,
          useIntegral: this.checked ? 1 : 0,
          couponId: 0,
          payType: this.pay == "pay1" ? "weixin" : "yue",
          pinkId: 0,
          seckill_id: 0,
          combinationId: 0,
          bargainId: 0,
          from: "weixinh5",
          mark: this.message,
          shipping_type: 1,
          store_id: 0,
        },
        query: {
          key: this.orderKey,
        },
      });
    },
    // 获取订单数据
    get_confirm() {
      let cartId = this.$route.params.id;
      this.axios.post("/order/confirm", { cartId }).then((d) => {
        this.confirmData = d.data.data;
        this.orderKey = this.confirmData.orderKey;
        if (this.confirmData.addressInfo) {
          this.payloadObj.addressId = this.confirmData.addressInfo.id;
        }
        this.updata_payload();
        console.log(this.confirmData);
      });
    },
    select_addr(type) {
      this.addrSelectShow = true;
    },
    updata_payload() {
      this.axios
        .post("/order/computed/" + this.orderKey, this.payloadObj)
        .then((d) => {
          this.priceList = d.data.data.result;
          console.log(d);
        });
    },
    set_addr(id) {
      this.payloadObj = Object.assign({}, this.payloadObj, { addressId: id });
      this.addrSelectShow = false;
      this.confirmData.addressInfo = true;
      this.updata_payload();
    },
    add_addr() {
      console.log(1);
      this.$router.push("/user/add_address");
    },
  },
  watch: {
    checked() {
      this.payloadObj = Object.assign({}, this.payloadObj, {
        useIntegral: this.checked ? 1 : 0,
      });
      this.updata_payload();
      console.log(this.checked);
    },
    pay() {
      let a = null;
      if (this.pay == "pay1") {
        a = "weixin";
      } else {
        a = "yue";
      }
      this.payloadObj = Object.assign({}, this.payloadObj, { payType: a });
      this.updata_payload();
    },
  },
};
</script>

<style lang="scss">
#app > .ord_submit {
  height: 100%;
  overflow: auto;
  background-color: #f5f5f5;
  .head {
    background-image: linear-gradient(0deg, #f5f5f5, #d64336);
    padding: 16px;
    padding-top: 20px;
    .box {
      position: relative;
      &::after {
        width: 100%;
        left: 0;
        bottom: 0;
        height: 2px;
        background-image: url(http://47.115.51.185/h5/img/line.05bf1c84.jpg);
        background-size: auto 100%;
        display: block;
        position: absolute;
        content: "";
      }
    }
    .top {
      display: flex;
      align-items: flex-end;
      div {
        flex: 1;
        text-align: center;
        background-color: #f7c1bd;
        color: #fb5150;
        // font-size: 1;
        font-weight: 600;
        padding: 12px;
      }
      .right {
        height: 2em;
        line-height: 0.5em;
        border-top-right-radius: 5px;
      }
      .left {
        background-color: white;
        color: #fc4c4c;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        position: relative;
        &::after {
          display: block;
          content: "";
          position: absolute;
          width: 0;
          height: 0;
          right: -13px;
          bottom: -31px;
          transform: rotate(-21deg);
          border: calc(16px + 1em) solid transparent;
          border-right-color: white;
        }
      }
    }
    .bottom {
      position: relative;
      background-color: white;
      padding: 16px;
      .name {
        font-weight: bold;
      }
      .name.van-ellipsis {
        font-size: 0.9em;
      }
      .addr2 {
        color: #6a6868;
        font-size: 0.8em;
        margin-top: 5px;
        span {
          color: #fc5f6c;
          margin-right: 1em;
        }
      }
      .arrow {
        color: #6a6868;
        position: absolute;
        right: 16px;
        top: 40%;
      }
      .addr {
        display: flex;
        justify-content: space-between;
        padding-bottom: 15px;
      }
      .van-action-sheet__cancel {
        color: white;
        background-color: red;
      }
      .content1 {
        padding: 16px;
        .addrbox {
          .addr1 {
            padding: 16px 0;
            display: flex;
            align-items: center;
            border-bottom: 1px solid #eeeeee;
            .right {
              font-size: 0.9em;
              margin-left: 1.5em;
              flex: 1;
              .info {
                font-size: 0.8em;
                margin-top: 0.5em;
              }
            }
            .fa-check {
              display: none;
            }
          }
          .addrselect {
            .fa-check {
              display: block;
            }
            color: red;
          }
        }
      }
    }
  }
  .shoplist {
    background-color: white;
    .sum {
      padding: 16px;
      border-bottom: 2px solid #f5f5f5;
      font-size: 0.9em;
    }
    .shop {
      position: relative;
      display: flex;
      padding: 16px;
      border-bottom: 1px solid #f5f5f5;
      .img {
        width: 70px;
        height: 70px;
        background-color: violet;
        border-radius: 5px;
        background-size: 100% 100%;
      }
      .info {
        position: relative;
        flex: 1;
        min-width: 0;
        padding: 0 16px;
        font-size: 0.9em;
        .sku {
          font-size: 0.9em;
          margin-top: 3px;
        }
        .price {
          position: absolute;
          bottom: 0;
          left: 16px;
          color: red;
          font-size: 0.8em;
        }
      }
      .num {
        width: 2em;
        font-size: 0.8em;
        color: #968686;
      }
    }
  }
  .body {
    background-color: white;
    box-sizing: border-box;
    padding: 0 16px;
    margin-top: 10px;

    > div {
      border-bottom: 1px solid rgb(245, 245, 245);
      padding: 15px 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .right {
        color: rgb(166, 153, 177);
        font-size: 0.9em;
      }
    }
    .jifen {
      display: flex;
      align-items: center;
      span {
        padding-top: 2px;
        color: red;
        font-size: 1.1em;
        margin: 0 5px;
      }
    }
    .beizhu {
      display: block;
      .van-cell {
        padding: 10px 0;
        textarea {
          padding: 12px;
          background-color: #f5f5f5;
        }
      }
    }
  }
  .pay {
    padding: 0 16px 10px 16px;
    background-color: rgb(255, 255, 255);
    margin-top: 10px;
    .title {
      padding: 16px 0;
    }
    .box {
      display: flex;
      margin-bottom: 10px;
      border: 1px solid #eeeeee;
      align-items: center;
      div {
        flex: 1;
        height: 2.5em;
        display: flex;
        justify-content: center;
        font-size: 0.9em;
        align-items: center;
      }

      .right {
        color: #aaaaaa;
        font-size: 0.8em;
        border-left: solid 1px #eee;
      }
    }
    input {
      display: none;
    }
    .select {
      border-color: #fc5445;
      .left {
        color: #fb5150;
      }
    }
  }
  .sumprice {
    margin-top: 10px;
    padding-bottom: 60px;
    > div {
      background-color: white;
      padding: 16px;

      display: flex;
      justify-content: space-between;
      align-items: center;
      .right {
        font-size: 0.9em;
        color: #868686;
      }
    }
  }
}
</style>