let MyPlug = {};
MyPlug.install = function (Vue, options) {
    
  
    
  
    // 3. 注入组件选项
    Vue.mixin({
      methods: {
          mix_set_data(url,name){
              this.$axios(url).then(data =>{
                  this[name] = data.data.data;
                  console.log(this[name]);
              })

          }
      }
     
    })
  
    
  }
  export default MyPlug