<template>
  <div class="index">
    <!-- 导航栏 -->
    <Tabbar></Tabbar>

    <!-- 搜索栏 -->
    <div class="search" @click="$router.push(`/search`)">
      <van-search shape="round" disabled placeholder="搜索商品">
        <template #left>
          <img :src="allData.logoUrl" />
        </template>
      </van-search>
    </div>
    <!-- 轮播图 -->
    <div class="banner">
      <van-swipe>
        <van-swipe-item
          show-indicators
          v-for="(image, index) in allData.banner"
          :key="index"
        >
          <img v-lazy="image.pic" />
        </van-swipe-item>
      </van-swipe>
    </div>

    <!-- 九宫产品展示 -->
    <div class="gridprduce">
      <van-grid :border="false" :icon-size="43">
        <van-grid-item
          v-for="(item1, index1) in allData.menus"
          :to="item1.wap_url"
          :key="index1"
          :icon="item1.pic"
          :text="item1.name"
        />
      </van-grid>
    </div>

    <!-- 垂直滚动   -->
    <div class="vernotice">
      <van-notice-bar
        :left-icon="newimg"
        mode="link"
        background="#fff"
        color="#666"
        :scrollable="false"
      >
        <van-swipe
          vertical
          class="notice-swipe"
          :autoplay="3000"
          :show-indicators="false"
        >
          <van-swipe-item
            v-for="(item2, index2) in allData.roll"
            :key="index2"
            @click="$router.push(item2.wap_url)"
            >{{ item2.info }}</van-swipe-item
          >
        </van-swipe>
      </van-notice-bar>
    </div>

    <!-- 商品1    -->
    <div class="shop1">
      <TitleMore
        :title="'快速选择'"
        :content="allData.info.fastInfo"
      ></TitleMore>
      <div class="scrollswipe">
        <van-swipe :loop="false" :width="100" :show-indicators="false">
          <van-swipe-item
            v-for="(item3, index3) in allData.info.fastList"
            :key="index3"
          >
            <div class="photo" @click="jump_goodlist(item3.id,item3.cate_name)">
              <img :src="item3.pic" alt="" />
              <div class="van-ellipsis">{{ item3.cate_name }}</div>
            </div>
          </van-swipe-item>
        </van-swipe>
      </div>
    </div>
    <div class="shop2"><div></div>
      <TitleMore
        :title="'精品推荐'"
        :content="allData.info.bastInfo"
      ></TitleMore>
      <div class="scrollswipe">
        <van-swipe :loop="false" :width="300" :show-indicators="false">
          <van-swipe-item
            v-for="(item4, index4) in allData.info.bastBanner"
            :key="index4"
          >
            <div class="photo">
              <img :src="item4.img" alt="" />
            </div>
          </van-swipe-item>
        </van-swipe>
      </div>
    </div>

    <!-- 商品列表1 -->
    <div class="goodslist1">
      <van-card @click="$router.push(`/detail/${item5.id}`)" v-for="(item5, index5) in allData.info.bastList" :key="index5">
        <template #thumb>
          <div
            :style="`backgroundImage:url(${item5.image});backgroundSize:100% 100%;height:100%`"
          ></div>
        </template>
        <template #title>
          <div style="fontsize: 1.3em; fontweight: 500" class="van-ellipsis">
            {{ item5.store_name }}
          </div>
        </template>
        <template #num>
          <van-icon name="cart-circle-o" size="25" color="red" />
        </template>
        <template #price>
          <div style="color: red; fontsize: 1.1em; fontweight: bold">
            ￥{{ item5.price }}
          </div>
        </template>
        <template #bottom>
          <div>
            <span style="color: black; fontweight: bold"
              >￥{{ item5.vip_price }}</span
            >
            <img
              style="height: 1em"
              src="data:image/png;base64,"
              alt=""
            />
            <span style="color: #aaa; marginleft: 1em"
              >已售{{ item5.sales }}</span
            >
          </div>
        </template>
      </van-card>
    </div>

    <!-- 热门面板  -->

    <div class="hotpan">
      <div class="bg">
        <van-notice-bar>
          <template #left-icon>
            <span style="fontweight: bold">热门榜单</span>
          </template>
          <template #default>
            <span style="fontsize: 0.8em; marginleft: 1em"
              >根据销量、搜索、好评等综合得出</span
            >
          </template>
          <template @click="show_more"  #right-icon>更多 &gt;</template>
        </van-notice-bar>
      </div>
      <div class="toplist">
        <div @click="jump_shop(item.id)"  v-for="(item, index) in  allData.likeInfo" :key="index">
          <div class="imgbox" >
            <img :src="rankNumImg[index]" />
            <img :src="item.image" />
          </div>
          <div class="abc">
            {{ item.store_name }}
          </div>
          <div style="color: red; font-size: 0.8em">${{ item.price }}</div>
        </div>
      </div>
    </div>

    <div class="shop3">
      <TitleMore
        :title="`首发新品 <span style='color:red;vertical-align: top;'>new~</span>`"
        :content="`多个优质商品最新上架`"
      ></TitleMore>
      <div class="scrollswipe">
        <van-swipe :loop="false" :width="130" :show-indicators="false">
          <van-swipe-item
            v-for="(item3, index3) in allData.info.firstList"
            :key="index3"
            @click="jump_shop(item3.id)"
          >
            <div class="photo">
              <div class="img">
                <img :src="item3.image" alt="" />
              </div>
              <div class="van-ellipsis" style="width: 90%; marginleft: 5%">
                {{ item3.store_name }}
              </div>
              <div class="price" style="color: red">￥{{ item3.price }}</div>
            </div>
          </van-swipe-item>
        </van-swipe>
      </div>
    </div>

    <div class="shoplist4">
      <TitleMore
        :title="`促销商品`"
        :content="`库存商品优惠促销活动`"
      ></TitleMore>
      <div class="list">
        <van-card @click="jump_shop(item.id)" v-for="(item, index) in allData.benefit" :key="index">
          <template #thumb>
            <div class="imgbox">
              <img :src="item.image" alt="" />
            </div>
          </template>
          <template #title>
            <div class="van-ellipsis">
              <span style="fontsize: 1.3em">{{ item.store_name }}</span>
            </div>
          </template>
          <template #origin-price>
            <div>日常价： ￥{{ item.ot_price }}</div>
          </template>
          <template #price-top>
            <div>
              <div class="sales">促销价： ￥{{ item.price }}</div>
            </div>
          </template>
          <template #num>
            <div>仅剩： {{ item.stock }}瓶</div>
          </template>
        </van-card>
      </div>
    </div>
  </div>
</template>

<script>
import TitleMore from "./../components/TitleMore";
import Tabbar from './../components/Tabbar'
export default {
  data() {
    return {
      rankNumImg: [
        "http://47.115.51.185/h5/img/one.2814de22.png",
        "http://47.115.51.185/h5/img/two.1ea1d146.png",
        "http://47.115.51.185/h5/img/three.2162defb.png",
      ],
      newimg:
        "data:image/png;base64,",
      allData: {
        logoUrl: "",
        info: {
          bastBanner: null,
          bastInfo: null,
          bastList: null,
          fastInfo: null,
          fastList: null,
          firstInfo: null,
          firstList: null,
          salesInfo: null,
        },
      },
    };
  },
  components: {
    TitleMore,
    Tabbar
  },
  created() {
    // 获取首页数据
    this.getdata();
    this.$store.commit("set_tabbarIndex", 0);
  },
  methods: {
    show_more(){
      this.$toast.fail("暂无")
    },
    jump_shop(id){
      // console.log();
      this.$router.push('/detail/'+id)
    },
    jump_goodlist(id,name){
      console.log(id);
      this.$router.push("/goods_list/ ?sid="+id+"&title="+name)
    },
    // 获取接口数据
    getdata: function () {
      if(this.$store.state.index){
        this.allData = this.$store.state.index;
      }else{
        this.$axios("/index").then((data) => {
        this.allData = data.data.data;
        this.$store.state.index = data.data.data;
        console.log(this.allData);
      });
      }
      
    },
  },
};
</script>

<style lang="scss" >
#app >.index {
    box-sizing: border-box;
    padding-bottom: 50px;
    .search {
      img {
        height: 22px;
        margin: 0 10px;
      }
      i {
        color: silver;
      }
    }
    .gridprduce {
      .van-grid-item__content {
        padding: 5px 8px;
      }
    }
    .banner {
      .my-swipe .van-swipe-item {
        color: #fff;
        font-size: 20px;
        line-height: 150px;
        text-align: center;
        background-color: #39a9ed;
      }
    }
    .vernotice {
      border: {
        top: 1px solid #f4f4f4;
        bottom: 1px solid #f4f4f4;
      }
      box-shadow: 0 0.1rem 0.3rem #f5f5f5;
      .notice-swipe {
        height: 40px;
        line-height: 40px;
      }
      font-size: 10px;
      i:nth-child(1) {
        img {
          position: relative;
          width: 55px;
        }
        padding-right: 10px;
        border-right: 1px silver solid;
        margin-right: 10px;
      }
    }
    .shop1 {
      margin: 15px 0;
      .scrollswipe {
        padding: 0 16px;
        .photo {
          width: 100px;
          img {
            display: block;
            width: 100%;
          }
          div {
            font-size: 0.5em;
            padding-bottom: 5px;
            width: 80%;
            text-align: center;
            margin-left: 10%;
            border: 0.1px solid #f5f5f5 {
              top-color: transparent;
              bottom-width: 2px;
            }
          }
        }
        .van-swipe-item:nth-child(3n) {
          div {
            border-bottom-color: red;
          }
        }
        .van-swipe-item:nth-child(3n + 1) {
          div {
            border-bottom-color: blue;
          }
        }
        .van-swipe-item:nth-child(3n + 2) {
          div {
            border-bottom-color: green;
          }
        }
      }
    }
    .shop2 {
      .scrollswipe {
        margin-top: 20px;
        padding: 0 16px;
        .van-swipe-item {
          height: 120px;
          .photo {
            width: 100%;
            height: 100%;
          }
          img {
            width: 100%;
            height: 100%;
          }
        }
      }
    }
    .goodslist1 {
      margin-top: 20px;
      .van-card {
        background-color: #fff;
        padding: 0 16px 10px 16px;
        .van-card__content {
          background-color: #fff;
          box-sizing: border-box;
          border-bottom: 2px solid #f5f5f5;
          padding-bottom: 0.5em;
        }
      }
    }
    .hotpan {
      height: 160px;
      position: relative;
      .bg {
        background-image: url("http://47.115.51.185/h5/img/index-bg.7128c2eb.png");
        height: 100px;
        background-size: 100% 100%;
        .van-notice-bar {
          background-color: transparent;
          color: #fff;
        }
      }
      .toplist {
        top: 35px;
        position: absolute;
        width: 90%;
        bottom: 0;
        left: 5%;
        background-color: white;
        height: 80%;
        border-radius: 10px;
        padding: 0 10px;
        box-shadow: 0 0 0.2rem -0.1rem #aaa;
        display: flex;
        > div {
          flex: 1;
          height: 100%;
          box-sizing: border-box;
          padding: 5px;
          .abc {
            width: 100%;
            height: 2.2em;
            overflow: hidden;
            font-size: 0.5em;
          }
          .imgbox {
            height: 70%;
            position: relative;
            img:nth-child(1) {
              position: absolute;
              height: 30%;
              width: 30%;
            }
            img:nth-child(2) {
              height: 100%;
              width: 100%;
            }
          }
        }
      }
    }
    .shop3 {
      margin: 10px 0;
      .scrollswipe {
        padding: 0 16px;
        margin-top: 15px;
        .photo {
          width: 120px;
          height: 170px;
          border: 1px solid #eee;
          border-radius: 10px;
          .img {
            height: 70%;
          }
          img {
            width: 100%;
            height: 100%;
          }
          div {
            font-size: 0.5em;
            padding-bottom: 5px;

            text-align: center;
          }
        }
      }
    }
    .shoplist4 {
      .van-card {
        .van-card__header {
          position: relative;
          &::after {
            display: block;
            content: "";
            position: absolute;
            width: 100%;
            height: 1px;
            border-bottom: 1px solid #eee;
            left: 0;
            bottom: -5px;
          }
        }
        background-color: #fff;
        .imgbox {
          width: 100%;
          height: 100%;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .van-card__origin-price {
          margin: 0;
        }
        .sales {
          box-sizing: border-box;
          background-color: rgb(255, 83, 54);
          display: inline-block;
          color: rgb(255, 244, 255);
          padding: 0 0.5em;
          margin-bottom: 4px;
          border-top-left-radius: 1em;
          border-bottom-right-radius: 1em;
        }
      }
    }
  }

</style>