<template>
  <div class="search">
    <div class="input">
      <van-search
        v-model="value"
        show-action
        shape="round"
        placeholder="请输入搜索关键词"
        @search="onSearch"
      >
        <template #action>
          <div @click="onSearch">搜索</div>
        </template>
      </van-search>
    </div>
    <div class="hot">
      <div class="title">热门搜索</div>
      <div class="content">
        <van-row>
          <van-col
            span="6"
            v-for="(v, i) in keyData"
            :key="i"
            @click="get_hotlist(v)"
            >{{ v }}</van-col
          >
        </van-row>
      </div>
      <van-divider />
    </div>
    <div class="isresult">
      <div class="hotshowlist" v-if="resultData && resultData.length == 0">
        <SquareShopList :data="hotData" :isshow="false"></SquareShopList>
      </div>
      <div class="searchlist" v-if="resultData && resultData.length > 0">
          <CardShopList :data="resultData"></CardShopList>
      </div>
    </div>
  </div>
</template>

<script>
import SquareShopList from "./../components/SquareShopList";
import CardShopList from "./../components/CardShopList";
export default {
  data() {
    return {
      value: "",
      
      keyData: null,
    //   热门搜索 
      hotData: null,
    //   搜索结果 
      resultData:null,
    };
  },
  components: {
    SquareShopList,
    CardShopList
  },
  created() {
    this.get_data("/search/keyword", "keyData");
     this.get_data("/product/hot?page=1&limit=20", "hotData");
  },
  methods: {
    onSearch(val) {
        let agent = this.value;
       if(agent.trim().length>0){
           this.get_search(this.value)
       }
    },
    get_data: function (url, name) {
      this.$axios(url).then((data) => {
        this[name] = data.data.data;
        console.log(this[name]);
      });
    },
    
    get_search:function(key){
        this.get_data("/products?page=1&limit=20&keyword="+key,"resultData")
    },
    get_hotlist: function (v) {
        console.log(v);
        this.get_search(v);
    },
  },
};
</script>

<style lang="scss">
#app > .search {
  width: 100%;
  height: 100%;
  .input {
    margin-bottom: 10px;
  }
  .hot {
    padding: 0 16px;
    .title {
      color: #999;
      margin-bottom: 5px;
    }
    .content {
      .van-col {
        border: 1px solid #999;
        text-align: center;
        width: 20%;
        padding: 5px 0;
        font-size: 0.8em;
        margin-right: 2%;
      }
    }
  }
  .hotshowlist {
    position: relative;
    box-sizing: border-box;
    background-color: white;
    margin-top: 20px;
    
  }
}
</style>