<template>
  <table class="goods_list" @scroll="gundong">
    <div class="search">
      <van-search
        v-model="searchValue"
        shape="round"
        background="rgb(233,51,35)"
        placeholder="搜索商品信息"
        show-action
        @search="onSearch"
      >
        <template #action>
          <van-icon v-show="arrangMethod" @click="arrangMethod = 0" name="qr" />
          <van-icon
            v-show="!arrangMethod"
            @click="arrangMethod = 1"
            name="qr-invalid"
          />
        </template>
      </van-search>
    </div>
    <div class="select"  @click="jjjj">
      <van-dropdown-menu>
        <van-dropdown-item
       
         disabled :title="shop_title" />
        <van-dropdown-item
          title="价格"
          v-model="selectValue1"
          :options="option1"
          @change="item_change(0)"
        />
        <van-dropdown-item
        @change="item_change(1)"
          title="销量"
          v-model="selectValue2"
          :options="option2"
        />
        <van-dropdown-item
          title="新品"
          v-model="selectValue3"
          :options="option3"
          @change="item_change(2)"
        />
      </van-dropdown-menu>
    </div>
    <div class="result" v-if="resultData">
      <div class="card" v-if="arrangMethod == 0">
        <CardShopList :data="resultData"></CardShopList>
      </div>
      <div class="sudoku" v-if="arrangMethod == 1">
        <SudokuShopList :data="resultData"></SudokuShopList>
      </div>
      <div class="noresult" v-if="resultData.length == 0">
        <SquareShopList :data="hotData"></SquareShopList>
      </div>
    </div>
  </table>
</template>

<script>
import CardShopList from "./../components/CardShopList";
import SudokuShopList from "./../components/SudokuShopList";
import SquareShopList from "./../components/SquareShopList";
export default {
  data() {
    return {
      searchValue: "",
      selectValue1: "",
      selectValue2: "",
      selectValue3: 0,
      option1: [
        { text: "默认排序", value: "" },
        { text: "价格升序", value: "asc" },
        { text: "价格降序", value: "desc" },
      ],

      option2: [
        { text: "默认排序", value: "" },
        { text: "销量升序", value: "asc" },
        { text: "销量降序", value: "desc" },
      ],
      option3: [
        { text: "默认", value: 0 },
        { text: "新品", value: 1 },
      ],
      //   1 为九宫格
      resultData: null,
      arrangMethod: 0,
      hotData: null,
      // 最后关键字
      lastKey: null,
      page: 1,
      sid:0,
      shop_title:"默认"
    };
  },
  components: {
    SudokuShopList,
    CardShopList,
    SquareShopList,
  },
  created() {
    console.log(this.$route);
    this.get_hot();
    // 获取参数
    this.sid = this.$route.query.sid;
    if(this.sid == undefined){
      this.sid = 0

    }
    if(this.$route.query.title){
      this.shop_title = this.$route.query.title;

    }
    console.log('sid ===>',this.sid);
    
    this.searchValue = this.$route.params.key;
    if(this.searchValue == ' '){
      this.searchValue = ''

    }
    this.onSearch();
  },
  methods: {
    // 获取热门数据
    get_hot: function () {
      this.$axios("/product/hot?page=1&limit=20").then((data) => {
        console.log(data);
        this.hotData = data.data.data;
      });
    },
    // 搜索事件
    onSearch: function () {
      this.resultData = [];
      this.page = 1;
      this.get_search(this.searchValue, this.page);
      this.lastKey = this.searchValue;
      console.log("search", this.searchValue);
    },
    // 请求搜索结果
    get_search: function (key, page) {
      this.$axios(
        `/products?page=${page}&limit=8&keyword=${key}&sid=${this.sid}&news=${this.selectValue3}&priceOrder=${this.selectValue1}&salesOrder=${this.selectValue2}&`
      ).then((data) => {
        this.page += 1;

        if (this.resultData) {
          this.resultData.push(...data.data.data);
        } else {
          this.resultData = data.data.data;
        }
        if (data.data.data.length == 0) {
          this.page = -1;
        }
      });
    },
    gundong: function (e) {
      // console.dir(e.target);
      let ele = e.target;
      let a = ele.scrollTop + ele.offsetHeight;
      let b =
        ele.lastElementChild.offsetTop + ele.lastElementChild.offsetHeight;
      if (a > b && this.page > 0) {
        console.log("到底了");
        this.get_search(this.lastKey, this.page);
      }
    },
    item_change(type){
      if(type){
        this.selectValue1 ="";
      }else{
        this.selectValue2 ="";
      }
      this.onSearch();
    },
    jjjj(e){
    if(e.target.className == "select"){
      this.$router.push("/category")
    }
    }
  },
 
};
</script>

<style lang="scss">

#app > .goods_list {
  display: block;
  height: 100%;
  overflow: auto;
  width: 100%;
  .search {
    .van-search__action {
      font-size: 1.5em;
      color: white;
      &:active {
        background-color: rgb(233,51,35);
      }
    }
  }
  .result {
    .card {
      .van-card__num {
        display: none;
        & + div {
          span:last-child {
            float: right;
          }
          &::after {
            display: table;
            content: "";
            clear: both;
          }
        }
      }
    }
  }
  .select {
    position: relative;
    &::after{
      content: '';
      display: block;
      position: absolute;
      left: 30px;
      top: 15px;
      width: 2em;
      background-color: transparent;
      height: 1em;
    }
    .van-dropdown-menu__bar {
      div:nth-child(1) {
        .van-ellipsis {
          color: black;
        }
        .van-dropdown-menu__title {
          &::after {
            display: none;
          }
        }
      }
    }
  }
}
</style>