<template>
  <div class="detail" ref="detail">
    <van-tabs
      v-if="detailData"
      class="tabs"
      scrollspy
      sticky
      @scroll="tabs_scroll"
    >
      <van-tab title="商品">
        <div class="shop">
          <!-- 轮播图  -->
          <div class="swiper">
            <van-swipe @change="onChange">
              <van-swipe-item
                v-for="(v, i) in detailData.storeInfo.slider_image"
                :key="i"
              >
                <van-image width="375" height="375" lazy-load :src="v" />
              </van-swipe-item>

              <template #indicator>
                <div class="custom-indicator">
                  {{ current + 1 }}/{{
                    detailData.storeInfo.slider_image.length
                  }}
                </div>
              </template>
            </van-swipe>
          </div>
          <!-- 分享 -->
          <div class="sharp">
            <div class="left">
              ￥{{ detailData.storeInfo.price }}
              <span
                >￥{{ detailData.storeInfo.vip_price }}
                <img
                  src="data:image/png;base64,"
                  alt=""
                />
              </span>
            </div>
            <div class="right" @click="showShare = true">
              <van-icon name="share-o" />
            </div>
            <van-share-sheet
              cancel-text=""
              v-model="showShare"
              :options="options"
              @select="onSelect"
            />
          </div>

          <div class="pan">
            <div class="title">{{ detailData.storeInfo.store_name }}</div>
            <div class="other">
              <div>原价:￥{{ detailData.storeInfo.ot_price }}</div>
              <div>库存:{{ detailData.storeInfo.stock }}</div>
              <div>销量:{{ detailData.storeInfo.fsales }}</div>
            </div>
          </div>
        </div>
      </van-tab>
      <van-tab class="valuation" title="评价">
        <div class="left">用户评价(0)</div>
        <div class="right"><span>0%</span>好评率 {{ ">" }}</div>
      </van-tab>
      <van-tab class="recommend" title="推荐">
        <div class="title">☽ 优品推荐 ☾</div>
        <van-swipe :loop="false" class="my-swipe" indicator-color="white">
          <van-swipe-item
            v-for="(v1, i1) in detailData.good_list.length / 6"
            :key="i1"
          >
            <div class="shoplist">
              <div
                class="shop"
                @click="
                  $router.push(
                    '/detail/' +
                      detailData.good_list[(v1 - 1) * 6 + (v2 - 1)].id
                  )
                "
                v-for="(v2, i2) in 6"
                :key="i2"
              >
                <div class="img">
                  <img
                    :src="detailData.good_list[(v1 - 1) * 6 + (v2 - 1)].image"
                    alt=""
                  />
                </div>
                <div class="van-ellipsis">
                  {{ detailData.good_list[v1 * (v2 - 1)].store_name }}
                </div>

                <div class="price">
                  ￥{{ detailData.good_list[v1 * (v2 - 1)].price }}
                </div>
              </div>
            </div>
          </van-swipe-item>
        </van-swipe>
      </van-tab>
      <van-tab class="product" title="详情">
        <div class="title">产品介绍</div>
        <div class="end" v-html="detailData.storeInfo.description"></div>
      </van-tab>
    </van-tabs>
    <van-goods-action v-if="detailData" class="goods">
      <van-goods-action-icon icon="chat-o" text="客服" color="#ee0a24" />
      <van-goods-action-icon
        :icon="detailData.storeInfo.userCollect ? `star` : `star-o`"
        text="收藏"
        color="rgb(233,51,35)"
        @click="set_collect"
      />

      <van-goods-action-icon
        icon="cart-o"
        :badge="count.count ? count.count : ``"
        text="购物车"
        @click="set_cate"
      />
      <van-goods-action-button
        type="warning"
        @click="set_sku(0)"
        text="加入购物车"
      />
      <van-goods-action-button
        @click="set_sku(1)"
        type="danger"
        text="立即购买"
      />
    </van-goods-action>
    <van-sku
      v-if="sku"
      v-model="show"
      :sku="sku"
      :goods="sku.goods"
      :goods-id="id"
      :hide-stock="sku.hide_stock"
      ref="vansku"
      :reset-selected-sku-on-hide="true"
    >
      <template #sku-actions>
        <div></div>
      </template>
    </van-sku>
  </div>
</template>

<script>
export default {
  // 数据
  data() {
    return {
      detailData: null,
      showShare: false,

      current: 0,
      options: [
        {
          name: "生成海报",
          icon: "poster",
        },
      ],
      code: null,
      count: {
        count: 0,
      },
      show: false,
      sku: null,
    };
  },
  props: ["id"],
  computed: {},
  // 生命周期函数
  created() {
    // 请求数据
    this.get_detailData("/product/detail/" + this.id, "detailData");
  },
  // 其他
  methods: {
    get_detailData: function (url, name) {
      this.$axios(url).then((data) => {
        if (data.data.data) {
          this[name] = data.data.data;
          let abc = data.data.data;
          let imgUrl = "";
          let list = [];
          let proObj = abc.productValue;
          for (let i in proObj) {
            imgUrl = proObj[i].image;
            let suk = proObj[i].suk.split(",");
            let aaa = {};
            for (let l = 0; l < suk.length; l++) {
              aaa["s" + (1 + l)] = suk[l];
            }
            let obj = {
              uniqueId: proObj[i].unique,
              id: proObj[i].product_id,
              price: proObj[i].price, //价格
              ...aaa,
              stock_num: proObj[i].stock, //库存
            };
            list.push(obj);
          }
          let tree = [];
          abc.productAttr.forEach((val, i) => {
            let v = val.attr_values.map((val1) => {
              return {
                name: val1,
                imgUrl: i == 0 ? imgUrl : undefined,
                id: val1,
              };
            });
            let obj = {
              k: val.attr_name,
              k_s: "s" + (i + 1),
              k_id: v.product_id,
              v,
            };
            tree.push(obj);
          });
          let a = {
            goods: {
              picture: abc.storeInfo.image,
            },
            tree,
            list,
            price: abc.storeInfo.price,
            stock_num: abc.storeInfo.stock,
            none_sku: false,
            hide_stock: false,
          };
          console.log(a);
          this.sku = a;
        }
        console.log(url);
        console.log(this[name]);
        console.log(this.sku);
      });
    },
    // 获取数据
    get_data: function (url, name) {
      this.$axios(url).then((data) => {
        if (data.data.data) {
          this[name] = data.data.data;
        }
        console.log(url);
        console.log(this[name]);
      });
    },
    // 滚动 改变透明度
    tabs_scroll: function (c) {
      let num = this.$refs.detail.scrollTop;
      let ele = document.querySelector(".tabs .van-sticky");
      ele.style.opacity = num / 300;
    },
    // 点击分享按钮
    onSelect(option) {
      // Toast(option.name);
      console.log(option);
      this.showShare = false;
    },
    // 设置收藏按钮
    set_collect: function (v) {
      // if(this.$stroe.)
      // console.log(this);
      if (this.$store.getters.isToken) {
        this.$axios("/product/detail/" + this.id).then((data) => {
          let result = data.data.data.storeInfo.userCollect;
          if (result) {
            this.$axios
              .post("/collect/del", { id: this.id, category: "product" })
              .then((data) => {
                if (data.data.status == 200) {
                  this.$toast.success("取消收藏");
                }
                this.get_detailData("/product/detail/" + this.id, "detailData");
              });
          } else {
            this.$axios
              .post("/collect/add", { id: this.id, category: "product" })
              .then((data) => {
                if (data.data.status == 200) {
                  this.$toast.success("收藏");
                }
                this.get_detailData("/product/detail/" + this.id, "detailData");
              });
          }
        });
      } else {
        this.$toast.fail("请登录");
        this.$router.push("/login");
      }
    },
    // 设置购物车
    set_cate: function () {
      // if(this.$store.getters.isToken){
      this.$router.push("/cart");
      // }else{
      //   this.$toast.fail("请登录")
      //   this.$router.push("/login")
      // }
    },
    // 轮播
    onChange(index) {
      this.current = index;
    },
    // sku
    set_sku: function (type) {
      let skuData = this.$refs.vansku.getSkuData();
      console.log(skuData.selectedSkuComb);

      if (this.show) {
        if (type) {
          //  购买
          if (skuData.selectedSkuComb === null) {
            this.$toast.fail("请添加完整");
          } else {
            if (this.$store.getters.isToken) {
              console.log(skuData);
              this.$axios
                .post("/cart/add", {
                  cartNum: skuData.selectedNum,
                  new: 0,
                  productId: this.id,
                  uniqueId: skuData.selectedSkuComb
                    ? skuData.selectedSkuComb.uniqueId
                    : "",
                })
                .then((data) => {
                  if (data.data.status == 200) {
                    this.$router.replace(
                      "/order/submit/" + data.data.data.cartId
                    );
                  } else {
                    this.$toast.fail("购买失败");
                  }
                  this.show = false;
                });
            } else {
              this.$toast.fail("请登录");
              this.$router.push("/login");
            }
          }
        } else {
          //  加入
          if (skuData.selectedSkuComb === null) {
            this.$toast.fail("请添加完整");
          } else {
            if (this.$store.getters.isToken) {
              console.log(skuData);
              this.$axios
                .post("/cart/add", {
                  cartNum: skuData.selectedNum,
                  new: 0,
                  productId: this.id,
                  uniqueId: skuData.selectedSkuComb
                    ? skuData.selectedSkuComb.uniqueId
                    : "",
                })
                .then((data) => {
                  if (data.data.status == 200) {
                    this.$toast.success("添加成功");
                  }
                  this.show = false;
                  this.get_detailData(
                    "/product/detail/" + this.id,
                    "detailData"
                  );
                });
            } else {
              this.$toast.fail("请登录");
              this.$router.push("/login");
            }
          }
        }
      }

      this.show = true;
    },
  },
  watch: {
    detailData: function () {
      // console.log('detail change');
      this.get_data("/product/code/" + this.id, "code");
      this.get_data("/cart/count?numType=0", "count");
    },
    $route() {
      this.$refs.detail.scrollTo({
        top: 0,
        behavior: "smooth",
      });

      this.get_detailData(
        "/product/detail/" + this.$route.params.id,
        "detailData"
      );
    },
  },
};
</script>

<style lang="scss">
#app > .detail {
  height: 100%;
  width: 100%;
  position: relative;
  overflow: auto;
  background-color: rgb(245, 245, 245);
  .tabs {
    padding-bottom: 50px;
    .shop {
      background-color: white;
      padding-bottom: 10px;
      .swiper {
        .custom-indicator {
          position: absolute;
          right: 5px;
          bottom: 5px;
          padding: 2px 5px;
          font-size: 12px;
          background: rgba(0, 0, 0, 0.1);
        }
      }
      .sharp {
        display: flex;
        padding: 0 16px;
        justify-content: space-between;
        align-items: center;
        .van-share-sheet__options {
          display: flex;
          justify-content: center;
        }
        .left {
          font-weight: bold;
          font-size: 1.2em;
          color: rgb(252, 65, 65);
          span {
            font-size: 0.7em;
            color: black;
            img {
              height: 0.9em;
            }
          }
        }
        .right {
          color: #515151;
        }
        .van-popup {
          z-index: 4000 !important;
        }
      }
      .pan {
        padding: 5px 16px;

        .title {
          font-weight: bold;
        }
        .other {
          margin-top: 10px;
          display: flex;
          justify-content: space-between;
          color: rgb(130, 132, 143);
          font-size: 0.8em;
        }
      }
    }
    .valuation {
      background-color: white;
      padding: 10px 16px;
      margin-top: 10px;
      display: flex;
      justify-content: space-between;
      .left {
        font-size: 0.9em;
      }
      .right {
        color: rgb(128, 128, 128);
        font-size: 0.8em;
        span {
          color: red;
        }
      }
    }
    .recommend {
      background-color: white;
      margin-top: 10px;
      padding: 10px 16px;
      .title {
        text-align: center;
        padding-bottom: 10px;
        font-size: 0.9em;
        color: violet;
      }

      .my-swipe {
        padding-bottom: 20px;
        .shoplist {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          .shop {
            width: 30%;
            .img {
              height: 100px;
              img {
                width: 100%;
                height: 100%;
                border-radius: 5px;
              }
            }
            .van-ellipsis {
              font-size: 0.7em;
              margin-top: 5px;
            }
            .price {
              color: rgb(252, 65, 65);
              font-size: 0.7em;
            }
          }
        }
      }
      .van-swipe__indicators {
        bottom: 0px;
        .van-swipe__indicator {
          background-color: rgb(235, 235, 235);
          width: 8px;
          height: 8px;
        }
        .van-swipe__indicator--active {
          background-color: red !important;
        }
      }
    }
    .product {
      .title {
        text-align: center;
        padding: 10px;
        background-color: white;
        margin: 10px 0;
      }
      .end {
        div {
          width: 100vw !important;
        }
        .attributes-list::after {
          display: table;
          width: 0;
          height: 0;
          content: "";
          clear: both;
        }
      }
    }
  }
  .goods {
    z-index: 3000;
  }
}
</style>