<template>
  <div class="use_integral">
      <!-- 头部 -->
    <div class="headbox">
        <div class="currentScore">当前积分</div>
        <div class="c">{{signData.integral}}</div>
        <div class="line"></div>
        <div class="nav">
            <div class="item">
                <div class="num">{{signData.sum_integral}}</div>
                <div class="text">累计积分</div>
            </div>
            <div class="item">
                <div class="num">{{signData.deduction_integral}}</div>
                <div class="text">累计积分</div>
            </div>
            <div class="item">
                <div class="num">{{signData.today_integral}}</div>
                <div class="text">累计积分</div>
            </div>
        </div>
    </div>
    <!-- 内容 -->
    <div class="wrapper">
        <div class="title">
            <div class="del" :style="{color:`${flag==0?'red':'#aaa'}`,backgroundColor:`${flag==0?'#fff':'rgb(250,250,250)'}`,fontWeight:`${flag==0?'600':'400'}`}" @click="flag=0">分值明细</div>
            <div class="upc" :style="{color:`${flag==1?'red':'#aaa'}`,backgroundColor:`${flag==1?'#fff':'rgb(250,250,250)'}`,fontWeight:`${flag==1?'600':'400'}`}" @click="flag=1">分值提升</div>
        </div>
        <div class="hint" v-if="flag==0">
            <span>提示：积分数值的高低会直接影响您的会员等级</span> 
        </div>
        <!-- 内容 -->
        <div class="signitem"  v-if="flag==0">
            <div class="items" v-for="(v,index) in signItem" :key="index">
                <div class="left">
                    <div class="t">{{v.mark}}</div>
                    <div class="time">{{v.add_time}}</div>
                </div>
                <div class="point" :style="{color:`${v.pm?'#16ac57':'#fc4141'}`}">{{`${v.pm?'+':'-'}`}}{{v.number}}</div>
            </div>
        </div>
        <!-- 提升内容 -->
        <div class="tisheng">
            <router-link to="/">
                <div class="pic">
                    <van-image width="45" src="http://47.115.51.185/h5/img/score.30715cf5.png"/>
                </div>
                <div class="name">购买商品可获得积分奖励</div>
                <div class="earn">赚积分</div>
            </router-link>
            <router-link to="/user/sign">
                <div class="pic">
                    <van-image width="45" src="http://47.115.51.185/h5/img/score.30715cf5.png"/>
                </div>
                <div class="name">每日签到课获得积分奖励</div>
                <div class="earn">赚积分</div>
            </router-link>
        </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Integral',
  data(){
      return {
          activeName: 'a',
          flag:0,
          signData:[],
          signItem:[]
      }
  },
  created(){
    //   用户信息获取积分累计
      this.$axios.post("/sign/user",{
        all: 1,
        integral: 1,
        sign: 1
      }).then((data) => {
          this.signData = data.data.data;
      });
    //   /integral/list?page=1&limit=15
    this.$axios.get("/integral/list").then((data) => {
        this.signItem = data.data.data;
      });
  },
  components: {},
  methods:{
  }
}
</script>
<style  lang="scss">

#app > .use_integral{
    position: relative;
    // 头部
    .headbox{
        background-image: url(data:image/jpeg;base64,);
        background-repeat: no-repeat;
        background-size: 100% 100%;
        width: 100%;
        height: 230px;
        font-size: 14px;
        color: #fff;
        padding: 16px 0 28px 0;
        box-sizing: border-box;
        text-align: center;
        .currentScore{
            color: hsla(0,0%,100%,.8);
            text-align: center;
            margin-bottom: 8px;
        }
        .c{
            font-size: 36px;
        }
        .line{
            width: 30px;
            height: 2px;
            background-color: #fff;
            margin: 10px auto 0 auto;
        }
        .nav{
            display: flex;
            justify-content: space-around;
            margin-top: 20px;
            .num{
                font-size: 22px;
            }
            .text{
                color: hsla(0,0%,100%,.8);
                font-size: 12px;
            }
        }
    }
    // 内容
    .wrapper{
        overflow: hidden;
        width: 90%;
        position: absolute;
        top: 182px;
        left: 5%;
        .title{
            width: 100%;
            height: 50px;
            border-top-left-radius: 10px;
            border-top-right-radius: 10px;
            overflow: hidden;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 auto 10px;
            z-index: 3;
            div{
                height: 100%;
                flex: 1;
                background-color: rgb(248, 248, 248);
                color: #bbb;
                line-height: 50px;
                text-align: center;
            }
        }
        .hint{
            width: 100%;
            border-radius: 10px;
            margin: 0 auto;
            background-color: #fff5e2;
            padding: 4px 0;
            font-size: 13px;
            color: #c8a86b;
            text-align: center;
        }
        // 签到情况
        .signitem{
            margin-top: 10px;
            width: 100%;
            .items{
                width: 100%;
                height: 60px;
                border-bottom: 1px solid #ddd;
                display: flex;
                justify-content: space-between;
                align-items: center;
                .left{
                    .t{
                        color: black;
                        font-size: 14px;
                        text-align: left;
                    }
                    .time{
                        color: #777;
                        font-size: 12px;
                        text-align: left;
                        margin-top: 4px;
                    }
                }
                .point{
                    color: #16ac57;
                }
            }
        }
        // 分值提升
        .tisheng{
            width: 100%;
            a{
                display: flex;
                justify-content: center;
                align-items: center;
                color: #c8a86b;
                margin-bottom: 10px;
                .name{
                    text-align: left;
                    font-weight: 900;
                    font-size: 16px;
                    margin: 0 20px;
                }
                .earn{
                    padding: 2px 16px 4px;
                    border: 1px solid #c8a86b;
                    border-radius: 20px;
                    font-size: 12px;
                }
            }
        }
    }
}
</style>