<template>
    <div class="kslogin">
        <van-form @submit="login">
              <van-field
                v-model="username"
                name="phone"
                 left-icon="phone"
                placeholder="输入手机号码"
                :rules="[{ required: true,  }]"
              />
              <van-field
                v-model="password"
                type="number"
                name="captcha"
               left-icon="lock"
                placeholder="输入验证码"
                :rules="[{ required: true,  }]"
              >
              <template #right-icon>
                  <div class="code" @click="get_code">{{code}}</div>
              </template>
              </van-field>
             
              <div style="margin: 16px">
                <van-button class="btn" round block type="info" native-type="submit"
                  >登录</van-button
                >
              </div>
              <div class="foot">没有账号?<span @click="set_tab(3)">立即注册</span></div>
            </van-form>
    </div>
</template>
<script>

export default {
  
  data() {
    return {
      username: "",
      password: "",
      code:"获取验证码",
      // 获取验证码按钮是否可用，
      codebtn:true,
    };
  },
  props:['set_tab'],
  methods: {
    // 登录验证
    login(values) {
    //  console.log(values);
    this.$axios
        .post("/login/mobile", values)
        .then((data) => {
          if (data.data.status == 200) {
            this.$store.commit("set_token", data.data.data.token);
            console.log("成功获取到token");
            return this.$axios('/user');
          } 
          else {
            throw data.data.msg;
          }
        }).then(data =>{
          if(data.data.status == 200){
            console.log('成功获取用户数据',data);
            this.$store.commit("set_user", data.data.data);
            this.$router.push("/user");
          }else{
            throw data.data.msg;
          }
        })
        .catch((err) => {
          this.$toast.fail(err);
        });
    },
    // 获取验证码
    get_code:function(){
      // console.log('get_code');
      let key = this.$store.state.key;
      // 测试号码对不对
      if(/1\d{10}/.test(this.username) && this.codebtn){
        this.codebtn = false;
        let num = 61 ;
      let time = null;
      time = setInterval(() => {
        num--;
        this.code =  `还剩${num < 10 ? '0'+num  : num}秒`;
        if(num < 1){
          clearInterval(time);
          this.code = "获取验证码";
          this.codebtn = true;
        }
      }, 1000);

        // Toast.success('成功文案');
        this.$axios.post('/register/verify',{key,phone:this.username,type:'login'})
        .then(data => {
          if(data.data.status == 200){
            this.$toast.success(data.data.msg);
          }else{
            throw data.data.msg;
          }
        })
        .catch((err)=>{
          this.$toast.fail(err);
        })
      }else{
        this.$toast.fail('手机号码错误');
        
      }
      
    }
  },

};
</script>
<style lang="scss">
#app .login .kslogin{
  input {
    text-indent: 1em;
  }
  
  .btn{
    height: 40px;
    background: linear-gradient(90deg,#f35447 0,#ff8e3c);
    border: none;
  }
  .foot{
    font-size: .9em;
    text-align: center;
    color: silver;
    span{
      color: red;
    }
    
  }
  .code{
        background-color: rgb(243,84,70);
        color:white;
        padding: 0 .5em;
        font-size: .6em;
        border-radius: 2em;
        
    }
}
</style>

