<template>
  <div class="ord_list">
    <div class="head">
      <div class="info">
        <div>订单信息</div>
        <div class="brief">
          累计订单：{{ orderData.order_count }} 总消费：￥{{
            orderData.sum_price
          }}
        </div>
      </div>
      <div class="img">
        <img src="http://47.115.51.185/h5/img/orderTime.e95f9090.png" alt="" />
      </div>
    </div>
    <div class="body">
      <van-tabs v-model="active" animated>
        <van-tab title="标签 1">
          <template #title>
            <div class="f">待付款</div>
            <div class="f">{{ orderData.unpaid_count }}</div>
          </template>
          <div class="shoplist" v-if="listData">
            <div class="shop" v-for="(v, i) in listData" :key="i">
              <div class="title">
                <div class="time">{{ v._add_time }}</div>
                <div class="tip">未支付</div>
              </div>
              <div class="info">
                <div class="top">
                  <div
                    class="img"
                    :style="`background-image:url(${v.cartInfo[0].productInfo.image});background-size:100% 100%;`"
                  ></div>
                  <div class="word">
                    <div class="van-multi-ellipsis--l2">
                      {{ v.cartInfo[0].productInfo.store_name }}
                    </div>
                  </div>
                  <div class="money" style="text-align: right">
                    ￥{{ v.total_price }} <br />
                    x{{ v.total_num }}
                  </div>
                </div>
                <div class="bottom">
                  共{{ v.total_num }}件商品,总金额
                  <span>￥{{ v.pay_price }}</span>
                </div>
              </div>
              <div class="btns">
                <div class="qx" @click="cancal(v.order_id)">取消订单</div>
                <div class="lj" @click="pay(v.order_id)">立即付款</div>
                
              </div>
            </div>
          </div>
          <div v-if="listData && listData.length == 0">
            <van-empty
              class="custom-image"
              image="https://img01.yzcdn.cn/vant/custom-empty-image.png"
              description="暂无订单信息"
            />
          </div>
        </van-tab>
        <van-tab>
          <template #title>
            <div class="f">待发货</div>
            <div class="f">{{ orderData.unshipped_count }}</div>
          </template>
          <van-empty
            class="custom-image"
            image="https://img01.yzcdn.cn/vant/custom-empty-image.png"
            description="暂无订单信息"
          />
        </van-tab>
        <van-tab>
          <template #title>
            <div class="f">待收货</div>
            <div class="f">{{ orderData.received_count }}</div>
          </template>
          <van-empty
            class="custom-image"
            image="https://img01.yzcdn.cn/vant/custom-empty-image.png"
            description="暂无订单信息"
          />
        </van-tab>
        <van-tab>
          <template #title>
            <div class="f">待评价</div>
            <div class="f">{{ orderData.evaluated_count }}</div>
          </template>
          <van-empty
            class="custom-image"
            image="https://img01.yzcdn.cn/vant/custom-empty-image.png"
            description="暂无订单信息"
          />
        </van-tab>
        <van-tab>
          <template #title>
            <div class="f">已完成</div>
            <div class="f">{{ orderData.complete_count }}</div>
          </template>
          <van-empty
            class="custom-image"
            image="https://img01.yzcdn.cn/vant/custom-empty-image.png"
            description="暂无订单信息"
          />
        </van-tab>
      </van-tabs>
    </div>
    <van-action-sheet
    title="选择付款方式"
     v-model="show" :actions="actions" @select="onSelect" />
  </div>
</template>

<script>
export default {
  data() {
    return {
      orderData: {
        complete_count: 0,
        evaluated_count: 0,
        order_count: 0,
        received_count: 0,
        refund_count: 0,
        sum_price: 0,
        unpaid_count: 0,
        unshipped_count: 0,
      },
      active: 0,
      listData: null,
       actions: [{ name: '微信支付',subname:'使用微信快捷支付',paytype:"weixin" }, {paytype:"yue", name: '余额支付',subname:`使用当前用户余额支付`}],
       show:false ,
       shopId:null,
    };
  },
  computed: {
    user() {
      return this.$store.state.user;
    },
  },
  created() {
    this.mix_set_data("/order/data", "orderData");
    // console.log(this);
    this.mix_set_data("/order/list?page=1&limit=20&type=0", "listData");
  },
  methods: {
    cancal(id) {
      console.log(id);
      this.$axios.post("/order/cancel", { id }).then((data) => {
        if (data.data.status == 200) {
          this.mix_set_data("/order/list?page=1&limit=20&type=0", "listData");
          this.$toast.success(data.data.msg);
          this.mix_set_data("/order/data", "orderData");
        } else {
          this.$toast.fail(data.data.msg);
        }
      });
    },
    onSelect(item) {
      // 默认情况下点击选项时不会自动收起
      // 可以通过 close-on-click-action 属性开启自动收起
      // this.show = false;
      // this.$toast(item.name);
      // console.log(item);
      this.show = false;
      this.axios.post("/order/pay",{from:"weixinh5",paytype:item.paytype,uni:this.shopId}).then(data =>{
        if(data.data.status == 200){
          this.$toast.success(data.data.msg)
        }else{
          this.$toast.fail(data.data.msg)
        }
      })
    },
    pay(id){
      this.show =true;
      this.shopId = id;
      console.log(this.shopId);

    }
  },
};
</script>

<style lang="scss">
#app > .ord_list {
  height: 100%;
  background-color: rgb(245, 245, 245);
  .custom-image .van-empty__image {
    width: 90px;
    height: 90px;
  }
  .van-tab__pane-wrapper--inactive {
    overflow: hidden;
  }
  .head {
    position: relative;
    padding: 20px 16px;
    background-color: rgb(233, 51, 35);
    display: flex;
    justify-content: space-between;
    align-items: center;
    .img {
      background-color: rgb(233, 51, 35);
      height: 60px;
      width: 60px;
    }
    .info {
      color: white;
      font-weight: bold;

      .brief {
        font-size: 0.8em;
        margin-top: 10px;
        font-weight: normal;
      }
    }
    &::after {
      display: block;
      content: "";
      position: absolute;
      width: 100%;
      height: 40px;
      background-color: inherit;
      bottom: -40px;
      left: 0;
    }
  }
  .body {
    position: relative;
    padding: 0 16px;
    // padding-top: 16px;
    .van-tabs__wrap {
      // padding: 20px 0;
      height: 60px;
      border-radius: 5px;
    }
    .van-tab__text--ellipsis {
      display: inline;
    }
    .f {
      text-align: center;
    }
    .van-tab--active {
      font-weight: bold;
    }
    .van-tabs__content {
      background-color: white;
      border-radius: 5px;
      margin-top: 8px;
      padding: 0 16px;
    }
    .shoplist {
      .shop {
        .title {
          display: flex;
          justify-content: space-between;
          align-items: center;
          color: #fc4141;
          font-size: 0.9em;
          .time {
            color: black;
          }
          padding: 16px 0;
          position: relative;
          &::after {
            display: block;
            position: absolute;
            width: calc(100% + 32px);
            height: 2px;
            content: "";
            background-color: rgb(245, 245, 245);
            bottom: 0px;
            left: -16px;
          }
        }
        .info {
          padding: 16px 0;
          .top {
            display: flex;

            .img {
              flex: 1;
              width: 60px;
              height: 60px;
              background-color: red;
            }
            .word {
              flex: 2;
              font-size: 0.8em;
              padding: 0 20px;
              box-sizing: border-box;
            }
            .money {
              font-size: 0.8em;
              color: #999;
              flex: 1;
            }
          }
          .bottom {
            position: relative;
            text-align: right;
            font-size: 0.8em;
            margin-top: 10px;
            span {
              color: red;
              font-weight: bold;
            }
            padding-bottom: 16px;
            &::after {
              display: block;
              width: calc(100% + 32px);
              left: -16px;
              height: 2px;
              background-color: rgb(245, 245, 245);
              content: "";
              position: absolute;
              bottom: 0;
            }
          }
        }
        .btns {
          display: flex;
          justify-content: flex-end;
          padding-bottom: 20px;
          .qx,
          .lj {
            border: 1px solid black;
            padding: 0.2em 0.5em;
            font-size: 0.9em;
            border-radius: 1em;
          }
          .qx {
            color: rgb(170, 178, 200);
            border-color: rgb(170, 178, 200);
            margin-right: 5px;
          }
          .lj {
            color: white;
            border-color: rgb(233, 51, 35);
            background-color: rgb(233, 51, 35);
          }
          position: relative;
          &::after {
            display: block;
            height: 5px;
            width: calc(100% + 32px);
            background-color: rgb(245, 245, 245);
            content: "";
            position: absolute;
            left: -16px;
            bottom: 0px;
          }
        }
      }
    }
  }
}
</style>