<template>
  <div class="categroy">
    <!-- 导航栏 -->
    <Tabbar></Tabbar>

    <!-- 搜索栏  -->
    <div class="serch">
      <van-search
        @search="onSearch"
        shape="round"
        v-model="value"
        placeholder="搜索商品信息"
      />
    </div>
    <!-- 滚动导航 -->
    <div class="scrolltab">
      <van-index-bar
        v-if="categoryData != null"
        :index-list="categoryData.map((item, index) => item.cate_name)"
      >
        <template v-for="(item, index) in categoryData">
          <van-index-anchor :key="index" :index="item.cate_name">{{
            item.cate_name
          }}</van-index-anchor>
          <van-cell :key="index">
            <div @click="jump_goodlist(v.id,v.cate_name)" v-for="(v, i) in item.children" :key="i">
              <img :src="v.pic" />
              <p>{{ v.cate_name }}</p>
            </div>
          </van-cell>
        </template>
      </van-index-bar>
    </div>
  </div>
</template>
<script>
import Tabbar from "./../components/Tabbar";
export default {
  data() {
    return {
      categoryData: null,
      value: "",
    };
  },
  components: {
    Tabbar,
  },
  created() {
    // 获取首页数据
    this.getdata();
    this.$store.commit("set_tabbarIndex", 1);
  },
  methods: {
    // 获取接口数据
    getdata: function () {
      if (this.$store.state.category) {
        this.categoryData = this.$store.state.category;
      } else {
        this.$axios("/category").then((data) => {
          this.categoryData = data.data.data;
          this.$store.state.category = data.data.data;
          console.log(this.categoryData);
        });
      }
    },
    // 搜索
    onSearch: function () {
      let agent = this.value;
      if (agent.trim().length > 0) {
        this.$router.push("/goods_list/" + this.value);
      }
    },
    jump_goodlist(id,name){
     this.$router.push("/goods_list/ ?sid="+id+"&title="+name)
    }
  },
};
</script>
<style lang="scss">
#app > .categroy {
  box-sizing: border-box;
  padding: 50px 0;

  height: 100%;
  width: 100%;
  position: relative;
  overflow: hidden;
  .serch {
    position: fixed;
    z-index: 100;
    width: 100vw;
    height: 50px;
    left: 0;
    top: 0;
  }
  .scrolltab {
    height: 100%;
    overflow: auto;
    background-color: rgb(247, 247, 247);

    > div {
      box-sizing: border-box;
      padding-left: 100px;
      > div:nth-last-child(1) {
        padding-bottom: 100vh;
      }
    }
    .van-index-anchor {
      background-color: white;
    }
    .van-index-anchor--sticky {
      text-align: center;
    }
    .van-index-bar__sidebar {
      left: 0;
      width: 100px;
      position: fixed;
      top: 50px;
      transform: none;
    }
    .van-index-bar__index {
      display: block;
      box-sizing: border-box;
      height: 2em;
      color: #424242;
      font-size: 0.9em;
      line-height: 2em;

      border-left: 3px solid transparent;
    }
    .van-index-bar__index--active {
      color: #fc4141;
      border-color: #fc4141;
      background-color: white;
    }
    .van-cell__value {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      div {
        width: 30%;
        img {
          width: 100%;
          display: block;
        }
        p {
          font-size: 0.8em;
          text-align: center;
        }
      }
    }
  }
}
</style>
