<template>
  <div class="change_password">
    <div class="phone">当前手机号：{{ phone }}</div>
    <div class="body">
      <!-- 输入密码 -->
      <van-field
        class="inp"
        placeholder="设置新密码"
        v-model="password[0]"
        type="password"
      />
      <van-field
        class="inp"
        placeholder="确认新密码"
        v-model="password[1]"
        type="password"
      />
      <van-field
        class="inp"
        v-model="sms"
        center
        clearable
        placeholder="填写验证码"
      >
        <template #button>
          <van-button @click="get_sms" size="small" type="primary"
            >获取验证码</van-button
          >
        </template>
      </van-field>
      <van-button @click="change_password" class="btn" round block type="info"
        >确认修改</van-button
      >
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      sms: "",
      password: ["", ""],
    };
  },
  computed: {
    phone() {
      let phone = this.$store.state.user.phone;
      let pre = phone.substring(0, 3);
      let last = phone.substring(7);
      return pre + "****" + last;
    },
  },
  methods: {
    get_sms() {
      this.axios
        .post("/register/verify", {
          code: "",
          key: this.$store.state.key,
          phone: this.$store.state.user.phone,
        })
        .then((d) => {
          // console.log(d);
          this.$toast(d.data.msg);
        });
    },
    change_password() {
      let z = this;
      if (z.password[0].length == 0) {
        z.$toast.fail("请输入密码");
      } else {
        if (z.password[0] == z.password[1]) {
          if (z.password[0].length > 5 && z.password[0].length < 9) {
            if (z.sms.length > 0) {
              this.axios
                .post("/register/reset", {
                  account: z.$store.state.user.phone,
                  captcha: z.sms,
                  code: "",
                  password: z.password[0],
                })
                .then((d) => {
                  // console.log(d);
                  // this.$toast(d);
                  // console.log(d);
                  if(d.data.status == 200){
                    this.$toast.success(d.data.msg)
                    z.$router.go(-1)
                  }else{
                    this.$toast.fail(d.data.msg)
                  }
                });
            } else {
              z.$toast.fail("请输入认证码");
            }
          } else {
            z.$toast.fail("密码长度为6~8");
          }
        } else {
          z.$toast.fail("密码不一致");
        }
      }
    },
  },
};
</script>

<style lang="scss">
#app > .change_password {
  height: 100%;
  overflow: auto;

  .phone {
    margin-top: 30px;
    text-align: center;
    font-size: 1.1em;
    font-weight: bold;
  }
  .body {
    font-size: 1em;
    margin-top: 20px;
    padding: 0 20px;
    input {
      font-size: 1.1em;
    }
    .inp {
      margin-bottom: 10px;
    }
    .van-button--primary {
      background-color: transparent;
      font-size: 1em;
      color: rgb(233, 51, 35);
      border: none;
    }
    .btn {
      margin-top: 50px;
      background-color: rgb(233, 51, 35);
      border: none;
      font-size: 1.1em;
    }
  }
}
</style>