<template>
  <div class="use_vip">
    <div class="banner">
      <van-swipe class="my-swipe" @change="toggle_swipe" :loop="false">
        <van-swipe-item
          v-for="(v, i) in vipData.banner"
          :key="i"
          :class="{ active: selectIndex == i }"
        >
          <div :style="`background-image:url(${v.img})`">
            <div class="top">
              <div class="title">{{ v.title }}</div>
              <div class="body">{{ v.body }}</div>
              <div class="tip fa fa-lock"> 该会员等级尚未解锁</div>
            </div>
          </div>
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="ask" v-if="taskData">
      <div class="head">
        <div class="left"> <i class="fa-bank fa"></i> 会员升级要求</div>
        <div class="right"><span>1</span>/2</div>
      </div>
      <div class="body"  v-for="(v,i) in taskData.task" :key="i">
        <div class="top">
          <div class="left">{{v.name}}</div>
          <div class="right">{{v.new_number >= v.number ? '已' : '未' }}满足条件</div>
        </div>
        <div class="press">
          <div :style="`width:${v.new_number / v.number * 100}%`"></div>
        </div>
        <div class="rate"><span>{{v.new_number}}</span>/{{v.number}}</div>
      </div>
    </div>
  </div>
</template>
<script>
import vipData from "./vip.js";
export default {
  data() {
    return {
      selectIndex: 0,
      vipData,
      taskData:null,
    };
  },
  created(){
    this.mix_set_data("/user/level/task/1","taskData")
  },
  methods: {
    toggle_swipe(index) {
      this.selectIndex = index;
       this.mix_set_data("/user/level/task/"+(index+1),"taskData")
    },
  },
};
</script>
<style lang="scss">
#app > .use_vip {
  height: 100%;
  background-color: rgb(245,245,245);
  .banner {
    background-color: rgb(35, 35, 35);
    padding: 20px 0;
    overflow: hidden;
    .my-swipe {
      width: 80%;
      margin-left: 10%;
      overflow: visible;
    }

    .van-swipe__indicators {
      display: none;
    }

    .van-swipe-item {
      transition: all 0.5s;
      transform: scale(0.9);
      box-sizing: border-box;
      // padding: 0 20px;
      > div {
        position: relative;
        background-color: red;
        background-size: 100% 100%;
        width: 100%;
        height: 0;
        padding-bottom: 52%;
        overflow: hidden;
        border-radius: 10px;
        .top {
          position: absolute;
          left: 16px;
          color:rgb(255,255,255);
          top: 16px;
          .title{
            font-size: 1.5em;
            font-weight: bold;
            
          }
          .body{
            font-weight: bold;
            font-size: .9em;
          }
          .tip{
            
            margin-top: 2em;
            font-size: .8em;
          }
        }
      }
    }
    .active {
      transform: scale(1);
    }
  }
  .ask{
    background-color: white;
    padding: 16px;
     .left{
        font-weight: bold;
      }
      .right{
        color: #999999;
        font-size: .9em;
        span{
          color:#ffae06;
        }
      }
    .head{
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 10px;
      i{
        color: #ffae06;
        transform: scale(1.2);
        margin-right: .5em;
      }
    }
    .body{
      margin-bottom: 10px;
      border-radius: 10px;
      background-color: rgb(249,249,249);
      padding: 16px 0;
      .top{
        font-size: .9em;
        display: flex;
        justify-content: space-between;
        padding: 0 16px;
        border-left: 3px solid #ffae06;
      }
      .press{
        
        margin-top: 15px;
        margin-bottom: 10px;
        width: calc(100% - 32px);
        margin-left: 16px;
        border-radius: 2.5px;
        height: 5px;
        position: relative;
        overflow: hidden;
        background-color:rgb(238,238,238);
        >div{
          position: absolute;
          left: 0;
          top:0;
          height: 100%;
          background-color: #ffae06;
          transition: all .5s;
         
        }

      }
      .rate{
        text-align: right;
        padding: 0 16px;

        font-size: .8em;
        color: #999999;
        span{
          color:#ffae06;
        }
      }
    }
  }
}
</style>

